<template lang="pug">
        div.live-hot
            template(v-if="isLoaded")
                div.banner
                    swiper(:options="swiperOptionBanner" ref="swiperBanner")
                        swiper-slide(v-for="item in dataBanner" key="")
                            a(:href="item.url")
                                img(v-lazy="item.image")
                        ul.swiper-pagination-banner(slot="pagination")
                div.live-list(v-if="dataList && dataList.length")
                    div.live-item(
                        v-for="item in dataList"
                        @click="goRoom(item.room_id, item.live_image)"
                    )
                        div.live-item-title
                            div.live-item-title-l
                                div.avator
                                    img(v-lazy="item.head_image")
                                div.info
                                    span(v-text="item.nick_name")
                                    div.tag(v-text="item.city")
                            div.live-item-title-r
                                span.watch(v-text="`${item.watch_number}在看`")
                        div.live-item-img
                            img(v-lazy="item.live_image")
                            i.live-status(v-text="item.live_state" v-if="item.live_state")
                        div.live-item-footer(
                            v-text="item.title"
                            v-if="item.title"
                        )
                    <div v-if="allLoaded" class="no-more-data">无更多数据</div>
                null-data(text="亲，暂无任何直播" v-else)
            div.mt-spinner-box(v-else)
                mt-spinner(
                    :type="0"
                    color="#333"
                )
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'vue-awesome-swiper/node_modules/swiper/dist/css/swiper.min.css'
import nullData from '@/components/nullData'
import { mapActions } from 'vuex'
export default {
    name: 'liveHot',
    props: ['type', 'listType'],
    components: {
        swiper,
        swiperSlide,
        nullData
    },
    data() {
        return {
            dataBanner: '',
            dataList: '',
            has_next: '',
            page: 2,
            autoFill: false,
            allLoaded: false,
            swiperOptionBanner: {
                initialSlide: 1,
                loop: true,
                autoplay: 2000,
                pagination: '.swiper-pagination-banner',
                paginationClickable: true
            },
            isLoaded: false
        }
    },
    created() {
        this.getData();
    },
    mounted() {

    },
    methods: {
        ...mapActions(['setLiveImage']),
        getData(isRefresh) {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/index' : this.api.getIndex();
            this.axios.get(axiosUrl)
                .then(res => {
                    if (res.data.status == 1) {
                        this.isLoaded = true;
                        this.dataBanner = res.data.banner;
                        this.dataList = res.data.list;

                        this.has_next = res.data.has_next;
                        this.page = 2;

                        this.$nextTick(function() {
                            this.$emit('init');
                            isRefresh && this.$emit("onTopLoaded");
                        });
                    }
                    else {
                        this.Toast(res.data.error || '操作失败');
                    }

                })
                .catch(err => {
                    console.log(err);
                });
        },
        loadTop(id) {
            // 下拉刷新加载
            this.getData(true);
        },
        loadBottom() {
            // 无限加载
            let self = this;
            if (self.has_next) {
                let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/index' : this.api.getIndex();
                self.axios.get(axiosUrl, {
                    params: {
                        page: self.page
                    }
                }).then(res => {
                    setTimeout(() => {
                        if (res.data.status == 1) {

                            self.has_next = res.data.has_next;
                            self.page = self.page + 1;

                            if (res.data.list && res.data.list.length && res.data.list.length > 0) {
                                for (let i = 0; i < res.data.list.length; i++) {
                                    self.dataList.push(res.data.list[i]);
                                }
                            }
                            self.$nextTick(function() {
                                self.$emit('init');
                            });
                        }
                        else {
                            self.Toast(res.data.error || '操作失败');
                        }
                        self.$emit("onBottomLoaded");
                    }, 500);
                })
                    .catch(err => {
                        self.Toast(err || '网络异常');
                        self.$emit("onBottomLoaded");
                    });
            }
            else {
                setTimeout(function() {
                    self.Toast({
                        message: '无更多数据',
                        duration: 1000
                    });
                    self.$emit("onBottomLoaded");
                }, 500);
                return false;
            }
        },
        goRoom(id, live_image) {
            this.$router.push({
                path: `/room/${id}`
            });
            this.setLiveImage(live_image);
        }
    },
    watch: {
        listType() {
            if (this.listType == this.type) {
                if (this.dataList) return;
                this.getData();
            }
        },
        dataList() {
            // 当页面数据发生改变,进行处理
            this.allLoaded = !Boolean(this.has_next);
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.banner {
    .swiper-container {
        width: 100%;
        height: 33.33vw;
        overflow: hidden;
        position: relative;
        & .swiper-slide {
            width: 100%;
            height: 100%;
            & .swiper-img-link {
                display: block;
                width: 100%;
                height: 100%;
            }
            & img {
                display: block;
                width: 100%;
                height: 100%;
            }
        }
        & .swiper-pagination-banner {
            position: absolute;
            bottom: 0;
            z-index: 999;
            text-align: center;
            & .swiper-pagination-bullet {
                background: #000;
                opacity: 0.5;
            }
            & .swiper-pagination-bullet-active {
                background: @color-theme;
                opacity: 0.8
            }
        }
    }
}

.live-item {
    margin-bottom: .5rem;
    background-color: #fff;
    a {
        color: #333;
    }
}

.live-item-title {
    height: 3rem;
    padding: .5rem;
    display: flex;
    justify-content: space-between;
    .avator {
        width: 2rem;
        height: 2rem;
        margin-right: .5rem;
        img {
            width: 100%;
            height: 100%;
            border-radius: 1rem;
        }
    }
    .info {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        span {
            line-height: 1;
        }
    }
}

.live-item-title-l {
    display: flex;
    .tag {
        line-height: .85rem;
        padding: 0 .5rem;
        font-size: .55rem;
        background-color: @color-theme;
        color: #fff;
        width: max-content;
        border-radius: 1rem;
    }
}

.live-item-img {
    position: relative;
    width: 100vw;
    height: 100vw;
    img {
        width: 100%;
        height: 100%;
    }
    .live-status {
        position: absolute;
        top: .5rem;
        right: .5rem;
        z-index: 9999;
        line-height: 1rem;
        padding: 0 .75rem;
        border: 1px solid #fff;
        border-radius: .8rem;
        font-size: .55rem;
        color: #fff;
        background-color: rgba(0, 0, 0, .3);
    }
}

.live-item-title-r {
    .watch {
        color: @fc-light;
        font-size: .6rem;
    }
}
.live-item-footer{
    height: 2.1rem;
    line-height: 2.1rem;
    padding: 0 .5rem;
    color: #a7a7a7;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}
</style>




