<template lang="pug">
    div.live-room-message
        ul.msg-box(id="msg_box")
            template(v-for="(item, index) in msg")
                template(v-if="item.type != 6")
                    li.msg-item(v-if="item.type == 9")
                        label.msg-label(v-html="`${item.nick_name}：`")
                        span.msg-text(
                            v-html="item.text"
                            style="color:#ffce6b"
                        )
                    template(v-else)
                        li.msg-item(v-if="item.ext.sender")
                            img.msg-level(:src="'/public/images/rank/rank_' + item.ext.sender.user_level + '.png'")
                            label.msg-label(
                                v-html="`${item.nick_name}：`"
                                @click="showUserInfo(item.ext.sender.user_id)"
                            )
                            span.msg-text(v-html="item.text")
                        li.msg-item(v-else)
                            label.msg-label(v-html="`${item.nick_name}：`")
                            span.msg-text(v-html="item.text")
</template>
<script>
export default {
    props: ['msg'],
    data () {
        return {
            user_level: 1
        }
    },
    methods: {
        showUserInfo(user_id) {
            this.$emit('showUserInfo', user_id);
        }
    },
    computed: {
        iconLevel() {
            let arr_user_level = [];
            for (let val of this.msg) {
                arr_user_level.push(require(`@/assets/img/rank/rank_${val.ext.sender.user_level}.png`));
            }
            return arr_user_level;
        }
    }
}
</script>

<style lang="less" scoped>
.live-room-message {
    padding: .5rem;
    padding-right: 1.75rem;
    height: 22vh;
    position: relative;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
}
.msg-box {
    max-height: 22vh;
    overflow: hidden;
    overflow-y: scroll;
    .msg-item {
        color: #fff;
        .msg-level {
            height: .7rem;
            margin-right: .2rem;
            display: inline;
        }
        .msg-label {
            color: #ffce6b;
        }
        .msg-text {
            color: #fff;
        }
        .msg-icon {
            height: 1rem;
        }
    }
}
</style>



