<template lang="pug">
    header.header-bar
        div.header-l
            div.header-logo
                img.header-logo-img(:src="appInit.h5_logo")
            a.header-download(href="/mapi/index.php?ctl=app_download") 下载APP 
        div.header-r
            span.header-search(@click="search")
                router-link(to="/index/search")
                    i.icon.iconfont &#xe601;
            //- router-link.header-recharge(to="/user/account") 充值
</template>
<script>
import { mapGetters } from 'vuex'
export default {
    methods: {
        search() {
            // 搜索
        }
    },
    computed: {
        ...mapGetters({
            appInit: 'getAppInit'
        })
    }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';
header {
    position: fixed;
    width: 100%;
    z-index: 999;
    background-color: #fff;
    padding: .5rem;
    height: 2.25rem;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ddd;
    z-index: 99999;
}

.header-l,
.header-r {
    display: flex;
}

.header-logo {
    margin-right: .5rem;
    img {
        height: 1.2rem;
    }
}

.header-download {
    border: 1px solid @color-red;
    color: @color-red;
    height: 100%;
    line-height: 1.25rem;
    padding: 0 .5rem;
    border-radius: 3px;
    font-size: .6rem;
}

.header-recharge {
    width: 2rem;
    height: 100%;
    line-height: 1.25rem;
    background-color: @color-red;
    color: #fff;
    border-radius: 100rem;
    font-size: .6rem;
    text-align: center;
    margin-left: .5rem;
}

.header-search {
    .iconfont {
        color: #5c606a;
    }
}
</style>



