<template lang="pug">
    div.live-item
        router-link(to="/live/room_app")
            div.live-item-title {{item.head_image}}
            //-     div.live-item-title-l
            //-         div.avator
            //-             img(:src="item.head_image")
            //-         div.info
            //-             span(v-text="item.nick_name")
            //-             div.tag(v-text="item.city")
            //-     div.live-item-title-r
            //-         span.watch(v-text="`${item.watch_number}在看`")
            //- div.live-item-img
            //-     img(src="../assets/img/avator-big.jpg")
                i.live-status 直播
</template>
<script>
export default {
    props: ['item'],
    mounted(){
        console.log(this.item);
    }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';
.live-item {
    margin-bottom: .5rem;
    background-color: #fff;
    a{
        color: #333;
    }
}

.live-item-title {
    height: 3rem;
    padding: .5rem;
    display: flex;
    justify-content: space-between;
    .avator {
        width: 2rem;
        height: 2rem;
        margin-right: .5rem;
        img {
            width: 100%;
            height: 100%;
            border-radius: 1rem;
        }
    }
    .info{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        span{
            line-height: 1;
        }
    }
}

.live-item-title-l {
    display: flex;
    .tag {
        line-height: .85rem;
        padding: 0 .5rem;
        font-size: .55rem;
        background-color: @color-theme;
        color: #fff;
        width: max-content;
        border-radius: 1rem;
    }
}

.live-item-img {
    position: relative;
    width: 100vw;
    height: 100vw;
    img {
        width: 100%;
        height: 100%;
    }
    .live-status{
        position: absolute;
        top: .5rem;
        right: .5rem;
        z-index: 9999;
        line-height: 1rem;
        padding: 0 .75rem;
        border: 1px solid #fff;
        border-radius: .8rem;
        font-size: .55rem;
        color: #fff;
        background-color: rgba(0, 0, 0, .3);
    }
}

.live-item-title-r {
    .watch {
        color: @fc-light;
        font-size: .6rem;
    }
}
</style>


