<template lang="pug">
    div.union
        template(v-if="isLoaded")
            div.union-list(style="display:flex;flex-wrap:wrap;margin:-1.1vw;" v-if="dataList && dataList.length")
                div.union-item(v-for="item in dataList")
                    router-link(:to="{ path: '/society/detail/index', query: { society_id: item.id }}")
                        img(v-lazy="item.society_image")
                        div.info
                            h3(v-text="item.society_name")
                            div
                                span.sp-l(v-html="`会长：${item.society_chairman}`") 
                                span.sp-r(v-text="`人数：${item.society_user_count}`")
                <div v-if="allLoaded" class="no-more-data">无更多数据</div>
            null-data(v-else)
        div.mt-spinner-box(v-else)
            mt-spinner(
                :type="0"
                color="#333"
            )
</template>
<script>
import nullData from '@/components/nullData'
export default {
    name: 'societyList',
    props: ['type', 'listType'],
    components: {
        nullData
    },
    data() {
        return {
            dataList: '',
            has_next: '',
            page: 2,
            allLoaded: false,
            isLoaded: false
        }
    },
    methods: {
        getData(isRefresh = false) {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_list' : this.api.getSocietyList();
            this.axios.get(axiosUrl)
                .then(res => {
                    this.isLoaded = true;
                    if (res.data.status == 1) {
                        this.dataList = res.data.list;
                        this.has_next = res.data.has_next;
                        this.page = 2;
                    }
                    else {
                        Toast(res.data.error || '请求出错');
                    }

                    this.$nextTick(function() {
                        this.$emit('init');
                        isRefresh && this.$emit("onTopLoaded");
                    });

                })
                .catch(err => {
                    Toast(err || '网络异常，请求失败');
                    isRefresh && this.$emit("onTopLoaded");
                });
        },
        loadTop(id) {
            // 下拉刷新加载
            this.getData(true);
        },
        loadBottom() {
            // 无限加载
            let self = this;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_list' : self.api.getSocietyList();
            if (self.has_next) {
                self.axios.get(axiosUrl, {
                    params: {
                        page: self.page
                    }
                })
                    .then(res => {
                        setTimeout(() => {
                            if (res.data.status == 1) {

                                self.has_next = res.data.has_next;
                                self.page = self.page + 1;

                                if (res.data.list && res.data.list.length && res.data.list.length > 0) {
                                    for (let i = 0; i < res.data.list.length; i++) {
                                        self.dataList.push(res.data.list[i]);
                                    }
                                }
                            }
                            else {
                                Toast(res.data.error || '请求出错');
                            }

                            self.$nextTick(function() {
                                self.$emit('init');
                                self.$emit("onBottomLoaded");
                            });
                        }, 500);
                    })
                    .catch(err => {
                        Toast(err || '网络异常，请失败');
                        self.$emit("onBottomLoaded");
                    });
            }
            else {
                setTimeout(function() {
                    Toast({
                        message: '无更多数据',
                        duration: 1000
                    });
                    self.$emit("onBottomLoaded");
                }, 500);
                return false;
            }
        }
    },
    watch: {
        listType() {
            if (this.listType == this.type) {
                if (this.dataList) return;
                this.getData();
            }
        },
        dataList() {
            // 当页面数据发生改变,进行处理
            this.allLoaded = !Boolean(this.has_next);
        }
    }
}
</script>
<style lang="less" scoped>
.union-list {
    display: flex;
    flex-wrap: wrap;
    margin: -1.1vw;
    .union-item {
        position: relative;
        width: 49vw;
        height: 49vw;
        margin: 1vw;
        img {
            width: 100%;
        }
        .info {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            color: #fff;
            background-color: rgba(0, 0, 0, .5);
            height: 2rem;
            padding: 0 .5rem;
            h3 {
                font-size: .6rem;
            }
            div {
                font-size: .5rem;
                display: flex;
                justify-content: space-between;
                line-height: 1;
                span {
                    width: 50%;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    &.sp-l {
                        width: 60%;
                        margin-right: .25rem;
                    }
                    &.sp-r {
                        width: 40%;
                        text-align: right;
                    }
                }
            }
        }
    }
}
</style>




