<template lang="pug">
    div.live-room-msg-form(
        :class="[{'show' : isShowMsgForm}]"
    )
        div.form-mask(@click="showMsgForm")
        div.form-main
            div.form-main-inner
                input(type="text" id="input-chat-speak" v-model="msgText" placeholder="和大家说点什么")
                div.btn-sendMsg(@click="sendMsg") 发送
</template>
<script>
export default {
    props: ['isShowMsgForm'],
    data() {
        return {
            msgText: ''
        }
    },
    mounted() {
        
    },
    methods: {
        sendMsg() {
            // 发送消息
            this.$emit('sendMsg', $.trim(this.msgText));
        },
        showMsgForm() {
            this.$emit('showMsgForm')
        }
    },
    watch: {
        isShowMsgForm() {
            this.msgText = '';
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.live-room-msg-form {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 999;
    display: flex;
    flex-direction: column;
    transform: translate3D(0, 100%, 0);
    &.show {
        transition: all .3s;
        transform: translate3D(0, 0, 0);
    }
    .form-mask {
        width: 100%;
        height: 100%;
        background-color: transparent;
    }
    .form-main {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 999;
        width: 100%;
        height: 2.6rem;
        padding: .5rem;
    }
    .form-main-inner {
        display: flex;
    }
    input {
        border: 0;
        height: 1.5rem;
        border-radius: 3px;
        padding: 0 .5rem;
        width: 100%;
    }
    .btn-sendMsg {
        height: 1.5rem;
        line-height: 1.5rem;
        padding: 0 .5rem;
        background-color: @color-theme;
        color: #fff;
        border-radius: 3px;
        width: 2.5rem;
        text-align: center;
        flex-shrink: 0;
        margin-left: .5rem;
    }
}
</style>




