<template lang="pug">
    nav.footer-bar.bar-tab
        router-link.tab-item(to="/")
           svg.svg-icon(aria-hidden="true")
                use(xlink:href="#icon-shouyexuanzhong" v-if="activeName == 'index'")
                use(xlink:href="#icon-shouyeweixuanzhong" v-else)
        router-link.tab-item(to="/rank/index")
           svg.svg-icon(aria-hidden="true")
                use(xlink:href="#icon-paihangbangxuanzhong" v-if="activeName == 'rankIndex'")
                use(xlink:href="#icon-paihangbangweixuanzhong" v-else)
        router-link.tab-item(to="/video/index")
            svg.svg-icon(aria-hidden="true")
                use(xlink:href="#icon-xiaoshipinxuanzhong" v-if="activeName == 'videoIndex'")
                use(xlink:href="#icon-xiaoshipinweixuanzhong" v-else)
        router-link.tab-item(to="/user/index")
            svg.svg-icon(aria-hidden="true")
                use(xlink:href="#icon-gerenxuanzhong" v-if="activeName == 'userIndex'")
                use(xlink:href="#icon-gerenweixuanzhong" v-else)
</template>
<script>
import Vue from 'vue'
import { Tabbar, TabItem } from 'mint-ui';
Vue.component(Tabbar.name, Tabbar);
Vue.component(TabItem.name, TabItem);
export default {
    props: ['activeName'],
    data(){
        return {
            selected: true
        }
    },
    mounted(){
    }
}
</script>
<style lang="less" scoped>
    @import '../assets/css/variable.less';
    .footer-bar{
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2.5rem;
        background-color: #fff;
        display: flex;
        align-items: center;
        z-index: 99999;
        .iconfont{
            font-size: 1.6rem;
            color: @color-theme;
        }
        .tab-item{
            flex-grow:1;
            text-align: center;
        }
        .svg-icon{
            width: 1.6rem;
            height: 1.6rem;
        }
    }
</style>





