<template lang="pug">
    div.live-room-contribution(v-show="isLoaded")
        div.live-room-contribution-num
            img.avator(
                :src="podcast.head_image"
                @click="showUserInfo(podcast.id)"
            )
            div.info
                div.title(v-if="liveIn==1") 直播
                div.title(v-else) 精彩回播
                div.num(v-text="viewer.watch_number")
            div.btn-follow(@click="follow" v-if="podcast.has_focus == 0") 关注    
        div.live-room-contribution-list(ref="scroll_wrapper")
            div.list-inner
                img.avator(v-for="item in viewer.list" key="" :src="item.head_image", alt="" @click="showUserInfo(item.user_id)")
        span.close-room(@click="closeRoom")
                i.icon.iconfont &#xe66e;
</template>
<script>
import scroll from 'better-scroll'
import queryString from 'queryString'
export default {
    props: ['podcast', 'viewer', 'roomId', 'liveIn', 'isLoaded'],
    data() {
        return {
            swiperOptionContribution: {
                freeMode: true,
                slidesPerView: 8,
                onSlideChangeEnd: swiper => {
                    // console.log('onSlideChangeEnd', swiper.realIndex)
                },
                onInit() {
                    $(".swiper-slide").css({ 'width': 'auto' });
                }
            },
        }
    },
    methods: {
        closeRoom() {
            // 关闭直播间，返回首页刷新数据
            this.$router.push({
                path: '/',
                query: {
                    neekRefresh: 1
                }
            })
        },
        follow() {
            this.axios.post(this.api.userFollow(), queryString.stringify({
                to_user_id: this.podcast.id,
                room_id: this.roomId
            }))
                .then(res => {
                    if (res.data.status == 1) {
                        this.podcast.has_focus = res.data.has_focus;
                    }
                    else {
                        this.Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    this.Toast(err || '网络异常,请求失败');
                });
        },
        showUserInfo(user_id) {
            this.$emit('showUserInfo', user_id);
        }
    },
    watch: {
        viewer() {
            let self = this;
            self.$nextTick(() => {
                setTimeout(function() {
                    self.scroll = new scroll(self.$refs.scroll_wrapper, {
                        scrollX: true,
                        click: true,
                        tap: true
                    }); 
                }, 500);
                
            });
        }
    }
}
</script>

<style lang="less" scoped>
.live-room-contribution {
    display: flex;
    justify-content: space-between;
    padding: 0 .5rem;
    margin-right: 2rem;
    overflow: hidden;
}

.live-room-contribution-num {
    display: flex;
    padding: .25rem;
    height: 1.6rem;
    border-radius: .75rem;
    background-color: rgba(0, 0, 0, .4);
    color: #fff;
    align-items: center;
    flex-shrink: 0;
    margin-right: .5rem;
    .avator {
        width: 1.5rem;
        height: 1.5rem;
        border-radius: .75rem;
        margin-right: .25rem;
        margin-left: -.15rem;
    }
    .avator:last-child {
        margin-right: 0;
    }
    .info {
        font-size: .5rem;
        margin-right: .25rem;
    }
    .btn-follow {
        line-height: 1.15rem;
        border-radius: 100rem;
        padding: 0 .35rem;
        background-color: #fee204;
        color: #333;
        font-size: .55rem;
        margin-left: .25rem;
    }
}

.live-room-contribution-list {
    width: 100%;
    height: 1.5rem;
    overflow: hidden;
    .list-inner {
        width: max-content;
        height: 100%;
    }
    .avator {
        width: 1.5rem;
        height: 1.5rem;
        border-radius: 100rem;
        margin-right: .375rem;
        float: left;
    }
}

.close-room {
    top: 0;
    right: .5rem;
    position: absolute;
    font-size: 1.6rem;
    line-height: 1;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 1.5rem;
    background-color: rgba(0, 0, 0, .4);
    display: flex;
    align-items: center;
    justify-content: center;
    .iconfont {
        color: #fff;
    }
}
</style>


