<template lang="pug">
    li
        router-link.item-content.item-link(:to="link || ''")
            div.item-media(v-if="itemIcon")
                svg.svg-icon(aria-hidden="true")
                    use(:xlink:href="itemIcon")
            div.item-inner
                div.item-title(v-text="itemTitle")
                div.item-after(v-text="itemAfter")
</template>
<script>
export default {
    props: ['link', 'itemIcon', 'itemTitle', 'itemAfter']
}
</script>



