<template lang="pug">
    div
        ul.video-list(v-if="list && list.length>0")
            li.video-item(v-for="(item, index) in list")
                router-link(:to="{ path: '/video/detail', query: { weibo_id: item.weibo_id }}")
                    div.video-item-inner
                        img.cover(:src="item.photo_image", alt="")
                        div.info
                            h3.title(v-text="item.nick_name")
                            div.info-main
                                img.avator(:src="item.head_image", alt="")
                                div.watch-num
                                    i.icon.iconfont &#xe651;
                                    span(v-text="item.video_count")
            <div v-if="allLoaded" class="no-more-data">无更多数据</div>
        null-data(v-else)
</template>
<script>
import { Toast } from 'mint-ui'
import nullData from '@/components/nullData'
export default {
    props: ['list', 'allLoaded'],
    components: {
        nullData
    }
}
</script>
<style lang="less" scoped>
.video-list {
    display: flex;
    flex-wrap: wrap;
    .video-item {
        width: 50vw;
        height: 50vw;
        padding: .125rem;
    }
    .video-item-inner {
        position: relative;
        width: 100%;
        height: 100%;
        .cover {
            width: 100%;
            height: 100%;
        }
        .info {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            color: #fff;
            padding: .5rem;
            .title {
                font-size: .65rem;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
            }
            .info-main {
                display: flex;
                justify-content: space-between;
                align-items: center;
            }
            .avator {
                width: 1.25rem;
                height: 1.25rem;
                border-radius: 1.25rem;
            }
            .watch-num {
                height: .9rem;
                line-height: .9rem;
                padding: 0 .375rem;
                background-color: rgba(0, 0, 0, .2);
                font-size: .6rem;
                display: flex;
                align-items: center;
                border-radius: .9rem;
                .iconfont {
                    margin-right: .25rem;
                }
            }
        }
    }
}
</style>



