<template lang="pug">
    div.place-other-list
        template(v-if="isLoaded")
            div.other-list(v-if="dataList && dataList.length")
                div.other-item(
                    v-for="item in dataList"
                    @click="goRoom(item.room_id, item.live_image)"
                )
                    div.other-item-inner
                        img(v-lazy="item.live_image")
                        div.region(v-text="item.city")
                <div v-if="allLoaded" class="no-more-data">无更多数据</div>
            null-data(text="亲，暂无任何直播" v-else)
        div.mt-spinner-box(v-else)
                mt-spinner(
                    :type="0"
                    color="#333"
                )
</template>
<script>
import nullData from '@/components/nullData'
import { mapActions } from 'vuex'
export default {
    name: 'liveOther',
    props: ['id', 'type', 'listTypeId'],
    components: {
        nullData
    },
    data() {
        return {
            dataList: '',
            has_next: '',
            page: 2,
            allLoaded: false,
            isLoaded: false
        }
    },
    methods: {
        ...mapActions(['setLiveImage']),
        getData(isRefresh) {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/other' : `${this.api.getIndexClassify()}&classified_id=${this.id}`;
            this.axios.get(axiosUrl)
                .then(res => {
                    this.isLoaded = true;
                    if (res.data.status == 1) {
                        this.dataList = res.data.list;
                        this.has_next = res.data.has_next;
                        this.page = 2;
                    }
                    else {
                        this.Toast(res.data.error || '请求出错');
                    }

                    this.$nextTick(function() {
                        this.$emit('init');
                        isRefresh && this.$emit("onTopLoaded");
                    });
                })
                .catch(err => {
                    this.Toast(err || '网络异常，请求失败');
                    isRefresh && this.$emit("onTopLoaded");
                });
        },
        loadTop() {
            // 下拉刷新加载
            this.getData(true);
        },
        loadBottom() {
            // 无限加载
            let self = this;
            if (self.has_next) {
                let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/other' : `${this.api.getIndexClassify()}&classified_id=${this.id}`;
                self.axios.get(axiosUrl, {
                    params: {
                        page: self.page
                    }
                }).then(res => {
                    setTimeout(() => {
                        let data = res.data;
                        if (data.status == 1) {

                            self.has_next = res.data.has_next;
                            self.page = self.page + 1;

                            if (res.data.list && res.data.list.length && res.data.list.length > 0) {
                                for (let i = 0; i < res.data.list.length; i++) {
                                    self.dataList.push(res.data.list[i]);
                                }
                            }
                        }
                        else {
                            self.Toast(data.error || '请求出错');
                        }

                        self.$nextTick(function() {
                            self.$emit('init');
                            self.$emit("onBottomLoaded");
                        });
                    }, 500);
                })
                    .catch(err => {
                        self.Toast(err || '网络异常，请求失败');
                        self.$emit("onBottomLoaded");
                    });
            }
            else {
                let self = this;
                setTimeout(function() {
                    self.Toast({
                        message: '无更多数据',
                        duration: 1000
                    });
                    self.$emit("onBottomLoaded");
                }, 500);
                return false;
            }
        },
         goRoom(id, live_image) {
            this.$router.push({
                path: `/room/${id}`
            });
            this.setLiveImage(live_image);
        }
    },
    watch: {
        listTypeId() {
            if (this.listTypeId == this.id) {
                if (this.dataList) return;
                this.getData();
            }
        },
        dataList() {
            // 当页面数据发生改变,进行处理
            this.allLoaded = !Boolean(this.has_next);
        }
    }
}
</script>
<style lang="less" scoped>
.other-list {
    display: flex;
    flex-wrap: wrap;
    .other-item {
        width: 33.33vw;
        height: 33.33vw;
        padding: .125rem;
        img {
            width: 100%;
            height: 100%;
        }
        .region {
            position: absolute;
            bottom: 0;
            left: 0;
            color: #fff;
            padding: .25rem;
            width: 100%;
            background-color: rgba(0, 0, 0, .5);
            font-size: .6rem;
        }
    }
    .other-item-inner {
        position: relative;
        width: 100%;
        height: 100%;
    }
}
</style>




