<template lang="pug">
    div.follow
        template(v-if="isLoaded")
            template(v-if="(list && list.length) || (playback && playback.length)")
                //- 关注的好友直播
                div.place-follow.place-live
                    h3 关注的好友
                    div.live-list(v-if="list && list.length")
                        div.live-item(
                            v-for="item in list"
                            @click="goRoom(item.room_id, item.live_image)"
                        )
                            div.live-item-title
                                div.live-item-title-l
                                    div.avator
                                        img(v-lazy="item.head_image")
                                    div.info
                                        span(v-text="item.nick_name")
                                        div.tag(v-text="item.city")
                                div.live-item-title-r
                                    span.watch(v-text="`${item.watch_number}在看`")
                            div.live-item-img
                                img(v-lazy="item.live_image")
                                i.live-status(v-text="item.live_state")
                    null-data(text="关注的人竟然没在直播" v-else)
                
                //- 关注的好友精彩回放
                div.place-follow.place-playback
                    h3 关注的好友精彩回放
                    div.playback-list(v-if="playback && playback.length")
                        div.playback-item(
                            v-for="item in playback"
                            @click="goRoom(item.room_id, item.head_image)"
                        )
                            div.playback-item-avator
                                img(v-lazy="item.head_image")
                            div.playback-item-info
                                div.playback-item-info-l
                                    p.title
                                        span(v-text="item.nick_name")
                                        span.time(v-text="item.begin_time_format")
                                    p.sub-title(v-text="item.title")
                                div.playback-item-info-r
                                    div.playback-item-watch
                                        i.icon.iconfont &#xe651;
                                        span(v-text="item.watch_number_format")
                    null-data(text="关注的人暂无回放" v-else)
            template(v-else)
                null-data(text="关注的人竟然没在直播")
        div.mt-spinner-box(v-else)
                mt-spinner(
                    :type="0"
                    color="#333"
                )
</template>
<script>
import liveList from '@/components/liveList'
import liveItem from '@/components/liveItem'
import nullData from '@/components/nullData'
import { mapActions } from 'vuex'
export default {
    name: 'liveFollow',
    props: ['type', 'listType'],
    components: {
        liveList,
        liveItem,
        nullData,
    },
    data() {
        return {
            list: '',
            playback: '',
            isLoaded: false
        }
    },
    created() {

    },
    methods: {
        ...mapActions(['setLiveImage']),
        getData(isRefresh = false) {
            let self = this;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/focus_video' : self.api.getFocusVideo();
            self.axios.get(axiosUrl)
                .then(res => {
                    if (res.data.status == 1) {
                        self.isLoaded = true;
                        self.playback = res.data.playback;
                        self.list = res.data.list;

                        self.$nextTick(function() {
                            self.$emit('init');
                            if (isRefresh) {
                                self.$emit("onTopLoaded");
                            }
                        });
                    }
                    else {
                        self.Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    self.Toast(err || '网络异常，请求失败');
                });
        },
        loadTop(id) {
            // 下拉刷新加载
            this.getData(true);
        },
        loadBottom() {
            let self = this;
            setTimeout(function() {
                self.Toast({
                    message: '无更多数据',
                    duration: 1000
                });
                self.$emit("onBottomLoaded");
            }, 500);
            return false;
        },
        goRoom(id, live_image) {
            this.$router.push({
                path: `/room/${id}`
            });
            this.setLiveImage(live_image);
        }
    },
    watch: {
        listType() {
            if (this.listType == this.type) {
                if (this.list) return;
                this.getData();
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import '../../assets/css/variable.less';
@import '../../assets/css/liveItem.less';
.place-follow {
    h3 {
        font-size: .6rem;
        line-height: 1.75rem;
        padding: 0 .5rem;
    }
}

.playback-list {
    background-color: #fff;
    margin-bottom: .5rem;
    .playback-item {
        display: flex;
        padding: .5rem;
        a {
            display: flex;
            width: 100%;
            color: @fc-main;
        }
        &:last-child {
            padding-bottom: 0;
            .playback-item-info {
                border-bottom: 0;
            }
        }
    }
    .playback-item-avator {
        width: 2rem;
        height: 2rem;
        margin-right: .5rem;
        flex-shrink: 0;
        img {
            width: 100%;
            height: 100%;
            border-radius: 100%;
        }
    }
    .playback-item-info {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding-bottom: .6rem;
        border-bottom: 1px solid #e6e8f1;
        margin-right: -.5rem;
        padding-right: .5rem;
        .title {
            font-size: .65rem;
            .time {
                font-size: .5rem;
                color: @fc-lighter;
                margin-left: .5rem;
            }
        }
        .sub-title {
            font-size: .5rem;
            color: @fc-light;
        }
        .playback-item-watch {
            font-size: .6rem;
            display: flex;
            justify-content: center;
            align-items: center;
            .iconfont {
                margin-right: .25rem;
            }
        }
    }
    .playback-item-info-l {
        line-height: 1.7;
    }
}

.place-live~.place-playback {
    margin-top: -.5rem;
}
</style>



