<template lang="pug">
    div.null.data
        img.null-img(src="../assets/img/null-data.png")
        div.null-text(v-text="text || '亲~暂无任何内容'")
</template>
<script>
export default {
    props: ['text']
}
</script>

<style lang="less" scoped>
.null.data {
    text-align: center;
    padding: 1rem .5rem;
    .null-img {
        height: 7.5rem;
        width: 7.5rem;
    }
    .null-text {
        color: #b8b8b8;
        font-size: .6rem;
        margin-top: .25rem;
    }
}
</style>



