<template lang="pug">
    div.mint-loadmore-bottom
        span(v-show="bottomStatus !== 'loading'" :class="{ 'rotate': bottomStatus === 'drop' }")
            img.img-loading(src="../assets/img/loading.png")
        span(v-show="bottomStatus === 'loading'")
            img.img-loading(src="../assets/img/loading.gif")
</template>
<script>
export default {
    props: ['bottomStatus']
}
</script>




