import Vue from 'vue'
import Router from 'vue-router'
import roomIndex from '@/views/room/index'
import index from '@/views/index/index'
import rankIndex from '@/views/rank/index'
import userIndex from '@/views/user/index'
import videoIndex from '@/views/video/index'
Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'index',
            component: index,
            meta: {
                title: '首页',
                showHeadBar: true,
                showFooterBar: true,
                requireAuth: true
            }
        },
        {
            path: '/index',
            name: 'index',
            component: index,
            meta: {
                title: '首页',
                showHeadBar: true,
                showFooterBar: true,
                requireAuth: true
            }
        },
        {
            path: '/room/:id',
            name: 'roomIndex',
            component: resolve => require(['@/views/room/index'], resolve),
            meta: {
                title: '',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/index/search',
            name: 'indexSearch',
            component: resolve => require(['@/views/index/search'], resolve),
            meta: {
                title: '搜索',
                showHeadBar: false,
                showFooterBar: false,
            }
        },
        {
            path: '/rank/index',
            name: 'rankIndex',
            component: rankIndex,
            meta: {
                title: '排行榜',
                showHeadBar: true,
                showFooterBar: true,
                requireAuth: true
            }
        },
        {
            path: '/video/index',
            name: 'videoIndex',
            component: videoIndex,
            meta: {
                title: '小视频',
                showHeadBar: true,
                showFooterBar: true,
                requireAuth: true
            }
        },
        {
            path: '/video/detail',
            name: 'videoDetail',
            component: resolve => require(['@/views/video/detail'], resolve),
            meta: {
                title: '小视频详情',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/index',
            name: 'userIndex',
            component: userIndex,
            meta: {
                title: '我的',
                showHeadBar: false,
                showFooterBar: true
            }
        },
        {
            path: '/user/others',
            name: 'userOthers',
            component: resolve => require(['@/views/user/others'], resolve),
            meta: {
                title: '用户主页',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/society/detail/index',
            name: 'societyDetailIndex',
            component: resolve => require(['@/views/society/detail/index'], resolve),
            meta: {
                title: '公会详情',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/society/create_society',
            name: 'CreateSociety',
            component: resolve => require(['@/views/society/create_society'], resolve),
            meta: {
                title: '创建公会',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/login/index',
            name: 'loginIndex',
            component: resolve => require(['@/views/login/index'], resolve),
            meta: {
                title: '会员登录',
                showHeadBar: false,
                showFooterBar: false,
                requireAuth: true
            }
        },
        {
            path: '/login/register',
            name: 'loginRegister',
            component: resolve => require(['@/views/login/register'], resolve),
            meta: {
                title: '手机注册',
                showHeadBar: false,
                showFooterBar: false,
                requireAuth: true
            }
        },
        {
            path: '/user/profit',
            name: 'profit',
            component: resolve => require(['@/views/user/profit/index'], resolve),
            meta: {
                title: '收益',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/profit/exchange',
            name: 'profitExchange',
            component: resolve => require(['@/views/user/profit/exchange'], resolve),
            meta: {
                title: '兑换',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/profit/extract_record',
            name: 'profitExtractRecord',
            component: resolve => require(['@/views/user/profit/exchange'], resolve),
            meta: {
                title: '领取记录',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/profit/alipay_cash',
            name: 'profitAlipayCash',
            component: resolve => require(['@/views/user/profit/alipay_cash'], resolve),
            meta: {
                title: '支付宝提现',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/profit/withdrawals',
            name: 'profitwithdrawals',
            component: resolve => require(['@/views/user/profit/withdrawals'], resolve),
            meta: {
                title: '提现',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/grade',
            name: 'userGrade',
            component: resolve => require(['@/views/user/grade'], resolve),
            meta: {
                title: '等级',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/tool/fans',
            name: 'userToolFans',
            component: resolve => require(['@/views/user/tool/fans'], resolve),
            meta: {
                title: '用户粉丝',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/tool/follow',
            name: 'userToolFollow',
            component: resolve => require(['@/views/user/tool/follow'], resolve),
            meta: {
                title: '用户关注',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/tool/small_video',
            name: 'userToolSmallVideo',
            component: resolve => require(['@/views/user/tool/small_video'], resolve),
            meta: {
                title: '用户小视频',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/tool/live_broadcast',
            name: 'userToolLiveBroadcast',
            component: resolve => require(['@/views/user/tool/live_broadcast'], resolve),
            meta: {
                title: '用户直播',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/dedicate',
            name: 'userDedicate',
            component: resolve => require(['@/views/user/dedicate'], resolve),
            meta: {
                title: '贡献排行榜',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/setting',
            name: 'userSettingIndex',
            component: resolve => require(['@/views/user/setting'], resolve),
            meta: {
                title: '设置',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/setting/safety',
            name: 'userSettingSafety',
            component: resolve => require(['@/views/user/setting/safety'], resolve),
            meta: {
                title: '账户与安全',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/setting/article_cate',
            name: 'userSettingArticleCate',
            component: resolve => require(['@/views/user/setting/article_cate'], resolve),
            meta: {
                title: '关于',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/setting/article_index',
            name: 'userSettingArticleIndex',
            component: resolve => require(['@/views/user/setting/article_index'], resolve),
            meta: {
                title: '关于',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/setting/help',
            name: 'userSettingHelp',
            component: resolve => require(['@/views/user/setting/help'], resolve),
            meta: {
                title: '帮助与反馈',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/setting/help_detail',
            name: 'userSettingHelpDetail',
            component: resolve => require(['@/views/user/setting/help_detail'], resolve),
            meta: {
                title: '',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/setting/faq',
            name: 'userSettingFaq',
            component: resolve => require(['@/views/user/setting/faq'], resolve),
            meta: {
                title: '',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/setting/blacklist',
            name: 'userSettingBlacklist',
            component: resolve => require(['@/views/user/setting/blacklist'], resolve),
            meta: {
                title: '',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/record',
            name: 'userRecord',
            component: resolve => require(['@/views/user/record/index'], resolve),
            meta: {
                title: '直播间消费记录',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/account',
            name: 'userAccountIndex',
            component: resolve => require(['@/views/user/account/index'], resolve),
            meta: {
                title: '充值',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/info/edit',
            name: 'userInfoEdit',
            component: resolve => require(['@/views/user/info/edit'], resolve),
            meta: {
                title: '编辑资料',
                showHeadBar: false,
                showFooterBar: false
            }
        },
        {
            path: '/user/ident',
            name: 'userIdentIndex',
            component: resolve => require(['@/views/user/ident/index'], resolve),
            meta: {
                title: '认证',
                showHeadBar: false,
                showFooterBar: false
            }
        },
    ]
})


