import Vue from 'vue'
import VueX from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(VueX)
const state ={
  onlineGuest:[],
  footerBarActiveName: 'index',
  isShowHeadBar: true,
  isShowFooterBar: true,
  appInit: '',
  userInfo: '',
  liveImage: ''
}
const mutations={
  SETONLINEGUEST(state,onlineGuest){
    state.onlineGuest = onlineGuest
  },
  set_footerBarActiveName(state,footerBarActiveName){
    state.footerBarActiveName = footerBarActiveName
  },
  SHOWHEADBAR(state,isShowHeadBar){
    state.isShowHeadBar = isShowHeadBar
  },
  SHOWFOOTERBAR(state,isShowFooterBar){
    state.isShowFooterBar = isShowFooterBar
  },
  set_appInit(state,appInit){
    state.appInit = appInit
  },
  set_userInfo(state,userInfo){
    state.userInfo = userInfo
  },
  set_liveImage(state,liveImage){
    state.liveImage = liveImage
  }
}
//创建store 实例
export default new VueX.Store({
  actions,
  getters,
  state,
  mutations
})



