const setOnlineGuest=({commit},onlineGuest) =>{
  commit('SETONLINEGUEST',onlineGuest)
}
const setFooterBarActiveName=({commit},footerBarActiveName) =>{
  commit('set_footerBarActiveName',footerBarActiveName)
}
const showHeadBar=({commit},isShowHeadBar) =>{
  commit('SHOWHEADBAR',isShowHeadBar)
}
const showFooterBar=({commit},isShowFooterBar) =>{
  commit('SHOWFOOTERBAR',isShowFooterBar)
}
const setAppInit=({commit},appInit) =>{
  commit('set_appInit',appInit)
}
const setUserInfo=({commit},userInfo) =>{
  commit('set_userInfo',userInfo)
}
const setLiveImage=({commit},liveImage) =>{
  commit('set_liveImage',liveImage)
}

export { setOnlineGuest, setFooterBarActiveName, showHeadBar, showFooterBar, setAppInit, setUserInfo, setLiveImage }


