<template lang="pug">
    div.live-room-income(v-show="isLoaded")
        span.anchor-income(v-text="`${appInit.ticket_name}：${parseInt(ticket)}`")
        span.anchor-id(v-text="`${appInit.account_name}：${ID}`")
</template>
<script>
import { mapGetters } from 'vuex'
export default {
    props: ['ticket', 'ID', 'isLoaded'],
    computed: {
        ...mapGetters({
            appInit: 'getAppInit'
        })
    }
}
</script>
<style lang="less" scoped>
    .live-room-income{
        display: flex;
        justify-content: space-between;
        padding: .5rem;
        .anchor-income{
            padding: .25rem .3rem;
            background-color: rgba(0, 0, 0, .4);
            color: #fff;
            border-radius: 100rem;
            line-height:1;
            font-size: .55rem;
        }
        .anchor-id{
            font-size: .6rem;
            color: rgba(255, 255, 255, .5);
        }
    }
</style>



