<template lang="pug">
    div.live-index
        header-bar
        div.content
            div.live-category(ref="scroll_wrapper")
                ul.swiper-pagination-category(slot="pagination")
            div.live-main
                mt-loadmore(
                    :top-method="loadTop"
                    :bottom-method="loadBottom"
                    :bottom-all-loaded="allLoaded"
                    :autoFill="autoFill"
                    ref="loadmore"
                    @top-status-change="handleTopChange"
                    @bottom-status-change="handleBottomChange"
                )
                    mint-loadmore-top(:topStatus="topStatus" slot="top")
                    swiper(:options="swiperOption" ref="swiperBanner" :style="{height: swiperContainerHeight+'px'}")
                        swiper-slide(v-for="item in liveCategory" key="")
                            div.live-container
                                template(v-if="item.type == 'index'")
                                    //- 热门
                                    live-hot(
                                        :type="item.type"
                                        :listType="listType"
                                        v-on:init="init"
                                        v-on:onTopLoaded="onTopLoaded"
                                        v-on:onBottomLoaded="onBottomLoaded"
                                        ref="hot"
                                    )
                                template(v-if="item.type == 'focus_video'")
                                    //- 关注
                                    live-follow(
                                        :type="item.type"
                                        :listType="listType"
                                        v-on:init="init"
                                        v-on:onTopLoaded="onTopLoaded"
                                        v-on:onBottomLoaded="onBottomLoaded"
                                        ref="follow"
                                    )
                                template(v-if="item.type == 'society'")
                                    //- 公会
                                    society-list(
                                        :type="item.type"
                                        :listType="listType"
                                        v-on:init="init"
                                        v-on:onTopLoaded="onTopLoaded"
                                        v-on:onBottomLoaded="onBottomLoaded"
                                        ref="society"
                                    )
                                template(v-if="item.type == 'other'")
                                    //- 其他
                                    live-other(
                                        :id="item.classified_id"
                                        :type="item.type"
                                        :listTypeId="listTypeId"
                                        v-on:init="init"
                                        v-on:onTopLoaded="onTopLoaded"
                                        v-on:onBottomLoaded="onBottomLoaded"
                                        ref="other"
                                    )
                    mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="isLoaded")
</template>
<script>
import Vue from 'vue'
import liveHot from '@/views/index/hot'
import liveFollow from '@/views/index/follow'
import liveOther from '@/views/index/other'
import liveItem from '@/components/liveItem'
import societyList from '@/views/society/list'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'vue-awesome-swiper/node_modules/swiper/dist/css/swiper.min.css'
import { mapGetters, mapActions } from 'vuex'
import scroll from 'better-scroll'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
import headerBar from '@/components/head'
export default {
    name: 'index',
    components: {
        swiper,
        swiperSlide,
        liveHot,
        liveFollow,
        liveOther,
        societyList,
        liveItem,
        mintLoadmoreTop,
        mintLoadmoreBottom,
        headerBar
    },
    data() {
        return {
            topStatus: '',
            bottomStatus: '',
            allLoaded: false,
            isLoaded: false,
            autoFill: false,
            listType: 'index', // 当前直播类型
            listTypeId: '',
            paginationWidth: 0,
            liveCategory: '',
            realIndex: 1,
            minSwiperContainerHeight: 0,
            swiperContainerHeight: null,
            totalCategoryWidth: 0,
            swiperOption: {
                initialSlide: 1,
                loop: false,
                pagination: '.swiper-pagination-category',
                paginationClickable: true,
                onInit: swiper => {
                    let self = this;
                    self.$nextTick(() => {
                        setTimeout(function() {
                            self.scroll = new scroll(self.$refs.scroll_wrapper, {
                                scrollX: true,
                                click: true,
                                tap: true
                            });
                        }, 500);
                    });
                }
            }
        }
    },
    beforeRouteEnter(to, from, next) {
        next(vm => {
            vm.setFooterBarActiveName('index');
            vm.$route.query.neekRefresh && vm.$refs.hot[0].loadTop();
            $(".page-content").scrollTop(0);
        });
    },
    created() {
        this.getLiveCategory();
    },
    mounted() {
        
    },
    methods: {
        ...mapActions(['setFooterBarActiveName']),
        getLiveCategory() {
            let video_classified = this.appInit.video_classified;
            let initLiveCategory = [{ "title": "关注", "type": "focus_video", "ctl": "index", "act": "focus_video" }, { "title": "热门", "type": "index", "ctl": "index", "act": "index" }, { "title": "公会", "type": "society", "ctl": "index", "act": "society" }];

            if (video_classified) {
                for (var i = 0; i < video_classified.length; i++) {
                    video_classified[i].type = 'other';
                    video_classified[i].typeId = 'other';
                    initLiveCategory.push(video_classified[i]);
                }
            }

            this.liveCategory = initLiveCategory;

        },
        getSwiperContainerHeight(data, hasListTop = false) {
            // 获取滑动列表实际高度
            data && (this.swiperContainerHeight = this.minSwiperContainerHeight > this.swiperContainerHeight + this.listTopHeight ? this.minSwiperContainerHeight : hasListTop ? this.swiperContainerHeight + this.listTopHeight : this.swiperContainerHeight) || (this.swiperContainerHeight = this.minSwiperContainerHeight > this.listTopHeight + this.NullDataHeight ? this.minSwiperContainerHeight : this.listTopHeight + this.NullDataHeight);
        },
        init() {
            let self = this,
                liveContainer_h = $(".swiper-slide-active").find(".live-container").height(),
                screen_h = screen.height;
            self.swiperContainerHeight = liveContainer_h > (screen_h - 130) ? liveContainer_h : (screen_h - 130);

            self.swiperOption.initialSlide = 1;
            self.isLoaded = true;
            self.swiperOption.onSlideChangeStart = (swiper) => {
                
                // 滑动改变菜单位置
                let $pagination = $(".swiper-pagination-category"),
                    $currentCategory = $pagination.find(".swiper-pagination-bullet-active"),
                    currentCategoryWidth = $currentCategory.width();

                if(swiper.realIndex > self.realIndex) { // 左滑
                    if(screen.width - $pagination.offset().left - $pagination.offset().width <= 0) { // 切换菜单是否到最右侧
                        if($currentCategory.offset().left > screen.width/2) { // 滑动选中的元素是否在屏幕中心的右侧，是则更改当前位置
                            self.totalCategoryWidth += currentCategoryWidth;
                            $pagination.css({'transform': 'translate(-' + self.totalCategoryWidth + 'px, 0px) translateZ(0px)'});
                        }
                    }
                   
                }
                else{ // 右滑
                    if(self.totalCategoryWidth <= currentCategoryWidth) { // 切换菜单是否到最左侧
                        self.totalCategoryWidth = 0;
                    }
                    else {
                        self.totalCategoryWidth -= $currentCategory.next().width(); 
                    }
                    $pagination.css({'transform': 'translate(-' + self.totalCategoryWidth + 'px, 0px) translateZ(0px)'});
                }

                $(".page-content").scrollTop(0);
                self.realIndex = swiper.realIndex;

                self.listType = self.liveCategory[swiper.realIndex].type;
                self.listTypeId = self.liveCategory[swiper.realIndex].classified_id;

                let liveContainer_h = $(".swiper-slide-active").find(".live-container").height(),
                    screen_h = screen.height;
                self.swiperContainerHeight = liveContainer_h > (screen_h - 130) ? liveContainer_h : (screen_h - 130);
            };
        },
        
        // 下拉刷新
        loadTop() {
            let self = this;
            switch (self.listType) {
                case 'index':
                    self.$refs.hot[0].loadTop();
                    break;
                case 'focus_video':
                    self.$refs.follow[0].loadTop();
                    break;
                case 'society':
                    self.$refs.society[0].getData(true);
                    break;
                case 'other':
                    self.$refs.other[0].getData(true);
                    break;
            }
            
        },

        // 滚动加载更多
        loadBottom() {
            let self = this;
            switch (self.listType) {
                case 'index':
                    self.$refs.hot[0].loadBottom();
                    break;
                case 'focus_video':
                    self.$refs.follow[0].loadBottom();
                    break;
                case 'society':
                    self.$refs.society[0].loadBottom();
                    break;
                case 'other':
                    self.$refs.other[0].loadBottom();
                    break;
            }
        },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        },
        handleBottomChange(status) {
            // 获取上拉最新状态
            this.bottomStatus = status;
        },
        onTopLoaded() {
            this.$refs.loadmore.onTopLoaded();
        },
        onBottomLoaded() {
            this.$refs.loadmore.onBottomLoaded();
        },
        firstLoaded() {
            this.isLoaded = true;
        }
    },
    watch: {
        liveCategory() {
            this.swiperOption.paginationBulletRender = (swiper, index, className) => {
                return `<li class="live-category-item ${className}"><span class="item-title">${this.liveCategory[index].title}</span></li>`;
            }
        }
    },
    computed: {
        ...mapGetters({
            appInit: 'getAppInit'
        })
    }
}
</script>
<style lang="less">
@import '../../assets/css/variable.less';
.live-index .content{
    padding-top: 2.25rem;
}
.live-category {
    position: fixed;
    left: 0;
    z-index: 9999;
    width: 100%;
    height: 1.75rem;
    line-height: 1.75rem;
    background-color: #fff;
    overflow: hidden; // overflow-x: scroll;
    // -webkit-overflow-scrolling: touch;
    .live-category-item {
        color: @fc-light;
        font-size: .65rem;
        padding: 0 10px;
        line-height: 100%;
    }
    .swiper-pagination-category {
        width: max-content;
        height: 100%;
        display: flex;
        justify-content: center;
        margin: 0 auto;
        .live-category-item {
            height: 1.75rem;
            line-height: 1.75rem;
            float: left;
        }
    }
    .swiper-pagination-bullet {
        width: initial;
        height: initial;
        background: none;
        opacity: 1;
        border-radius: 0;
        .item-title {
            display: block;
            width: max-content;
            height: 100%;
        }
    }
    .swiper-pagination-bullet-active {
        .item-title {
            border-bottom: 2px solid #333;
            color: #333;
        }
    }
}

.live-main {
    padding-top: 1.75rem;
}
</style>




