<template lang="pug">
    div.tip-weixin-share(@click="showTipWxShare")
        div
            i.iconfont.icon &#xe634;
            p 点击分享好友
</template>
<script>
export default {
    methods: {
        showTipWxShare() {
            this.$emit('showTipWxShare');
        }
    }
}
</script>

<style lang="less" scoped>
    .tip-weixin-share {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 9999;
        background-color: rgba(0, 0, 0, .8);
        color: #fff;
        text-align: right;
        padding: .5rem;
        .iconfont{
            font-size: 2rem;
            line-height: 1;
        }
        p{
            font-size: .6rem;
        }
    }
</style>




