<template lang="pug">
    div.live-room-interact
        div.interact-l
            span.block-interact
                i.icon.iconfont(@click.stop="showMsgForm") &#xe672;
        div.interact-r
            span.block-interact.block-interact-gift(@click="showGift")
                svg.svg-icon(aria-hidden="true")
                    use(xlink:href="#icon-liwu1")
            span.block-interact(@click="showTipWxShare")
                i.icon.iconfont &#xe673;
    
</template>
<script>
export default {
    methods: {
        showGift() {
            this.$emit('showGift')
        },
        showMsgForm() {
            this.$emit('showMsgForm')
        },
        showTipWxShare() {
            this.$emit('showTipWxShare')
        }
    }
}
</script>

<style lang="less" scoped>
    .live-room-interact{
        display: flex;
        justify-content: space-between;
        padding: .5rem;
        .iconfont{
            font-size: 1rem;
        }
        .block-interact{
            width: 1.75rem;
            height: 1.75rem;
            border-radius: 1.75rem;
            background-color: rgba(255, 255, 255, .8);
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .block-interact-gift{
            background-color: #fee204;
        }
    }
    .interact-r{
        display: flex;
        .block-interact{
            margin-left: .5rem;
        }
    }
</style>



