<template lang="pug">
    div.mint-loadmore-top
        span(v-show="topStatus !== 'loading'" :class="{ 'rotate': topStatus === 'drop' }")
            img.img-loading(src="../assets/img/loading.png")
        span(v-show="topStatus === 'loading'")
            img.img-loading(src="../assets/img/loading.gif")
</template>
<script>
export default {
    props: ['topStatus']
}
</script>



