<template lang="pug">
    div.content
        mt-loadmore(
            :top-method="loadTop"
            ref="loadmore"
            @top-status-change="handleTopChange"
        )
            mint-loadmore-top(:topStatus="topStatus" slot="top")
            div.user-index
                user-home-top(
                    :headImage="user.head_image || ''"
                    :nickName="user.nick_name || ''"
                    :iconSex="iconSex || ''"
                    :iconLevel="iconLevel || ''"
                    :ID="user.user_id || ''"
                    :signature="user.signature || ''"
                    :isAuthentication="user.is_authentication"
                    :vExplain="user.v_explain"
                    :vIcon="user.v_icon"
                )
                user-home-total(
                    :videoCount="user.video_count || '0'"
                    :sVideoCount="user.svideo_count|| '0'"
                    :focusCount="user.focus_count || '0'"
                    :fansCount="user.fans_count || '0'"
                )
                div.user-index-list
                    list-block  
                        list-item(itemIcon="#icon-songchu" itemTitle="送出" :itemAfter="`${user.use_diamonds || ''}${appInit.diamonds_name || ''}`")
                        list-item(link="/user/account" itemIcon="#icon-zhanghu" itemTitle="账户" :itemAfter="`${user.diamonds || ''}${appInit.diamonds_name || ''}`")
                        list-item(link="/user/profit" itemIcon="#icon-shouyi" itemTitle="收益" :itemAfter="`${user.ticket || ''}${appInit.ticket_name || ''}`")
                        //- list-item(itemIcon="#icon-IPhuiyuan" itemTitle="VIP会员" itemAfter="")
                        //- list-item(itemIcon="#icon-duihuanyouxibi" itemTitle="兑换游戏币" itemAfter="34245345游戏币")
                        list-item(link="/user/record" itemIcon="#icon-zhibojianshouzhijilu" itemTitle="直播间收支记录" itemAfter="")
                    list-block
                        list-item(link="/user/grade" itemIcon="#icon-dengji" itemTitle="等级" :itemAfter="`${user.user_level || ''}级`")
                        list-item(link="/user/ident" itemIcon="#icon-renzheng" itemTitle="认证" :itemAfter="authenticationText")
                        list-item(link="/user/dedicate" itemIcon="#icon-gongxianbang" :itemTitle="`${appInit.ticket_name || ''}排行榜`" itemAfter="")
                    //- list-block
                    //-     list-item(itemIcon="#icon-fenxiangshouyi" itemTitle="分享收益" itemAfter="")
                    //-     list-item(itemIcon="#icon-youxifenxiangshouyi" itemTitle="游戏分享收益" itemAfter="")
                    //- list-block
                    //-     list-item(itemIcon="#icon-shangpinguanli" itemTitle="商品管理" itemAfter="1个商品")
                    //-     list-item(itemIcon="#icon-wodedingdan" itemTitle="我的订单" itemAfter="0个订单")
                    //-     list-item(itemIcon="#icon-wodegouwuche" itemTitle="我的购物车" itemAfter="0个商品")
                    //- list-block
                    //-     list-item(itemIcon="#icon-wodejingpai" itemTitle="我的竞拍" itemAfter="1个竞拍")
                    //-     list-item(itemIcon="#icon-jingpaiguanli" itemTitle="竞拍管理" itemAfter="1个竞拍")
                    //- list-block
                    //-     list-item(itemIcon="#icon-wodexiaodian" itemTitle="我的小店" itemAfter="1个商品")
                    list-block
                        list-item(:link="`/society/detail/index?society_id=${user.society_id}`" itemIcon="#icon-wodegonghui" itemTitle="我的公会" v-if="user.society_id")
                        list-item(link="/society/create_society" itemIcon="#icon-wodegonghui" itemTitle="创建公会" v-else)
                        
                    list-block
                        list-item(link="/user/setting" itemIcon="#icon-shezhi" itemTitle="设置" itemAfter="")
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import listBlock from '@/components/listBlock'
import listItem from '@/components/listItem'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import userHomeTop from '@/components/user/userHomeTop'
import userHomeTotal from '@/components/user/userHomeTotal'
import { Indicator } from 'mint-ui'
export default {
    name: 'userIndex',
    components: {
        listBlock,
        listItem,
        mintLoadmoreTop,
        userHomeTop,
        userHomeTotal
    },
    data() {
        return {
            user: '',
            topStatus: '',
            iconLevel: '',
            iconSex: ''
        }
    },
    beforeRouteEnter(to, from, next) {
        next(vm => {
            vm.setFooterBarActiveName('userIndex');
            vm.popupVisible = false;
            $(".page-content").scrollTop(0);
        });
    },
    created() {
        this.getData();
    },
    methods: {
        ...mapActions(['setFooterBarActiveName']),
        getData(isRefresh) {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_home' : this.api.getUserHome();
            this.axios.get(axiosUrl).then(res => {
                if (res.data.status == 1) {
                    this.user = res.data.user;
                    this.iconLevel = require(`@/assets/img/rank/rank_${this.user.user_level}.png`);
                    this.iconSex = res.data.user.sex == 1 ? require(`@/assets/img/sex-man.png`) : require(`@/assets/img/sex-woman.png`);
                }
                else {
                    Toast(res.data.error || '请求出错');
                }
                this.$nextTick(() => {
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                });
            })
                .catch(err => {
                    Toast(err || '网络异常，请求失败');
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                });
        },
        loadTop() {
            this.getData(true);
        },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        }
    },
    computed: {
        ...mapGetters({
            appInit: 'getAppInit'
        }),
        authenticationText() {
            if(this.user.is_authentication == 0) {
                return '未认证';
            }
            else if(this.user.is_authentication == 1) {
                return '认证中';
            }
            else if(this.user.is_authentication == 2) {
                return '已认证';
            }
            else {
                return '认证失败';
            }
        }
    },
    activated() {
        this.getData();
    }
}
</script>



