<template lang="pug">
    div.live-room-gift(:class="[isShowGift ? 'show' : 'hide']")
        div.gift-mask(@click="showGift")
        div.gift-main(
            :class="[isShowGift ? 'show' : 'hide']"
            ref="giftMain"
        )
            swiper.swiper-container-gift(:options="swiperOptionBanner" ref="swiperBanner")
                swiper-slide.gift-list(v-for="giftItem in gift_list" key="")
                    li.gift-item(:class="{'selected' : item.id == selectedGift.prop_id}" v-for="item in giftItem" @click="select(item.id, item.diamonds)")
                        div.gift-icon
                            img(:src="item.icon", alt="")
                        h3(v-text="item.name")
                        div.price
                            img(src="../../../assets/img/icon-diamond.png", alt="")
                            span(v-text="item.diamonds")
                ul.swiper-pagination-gift(slot="pagination")
            div.gift-after
                span.gift-diamond(@click="showRecharge")
                    img(src="../../../assets/img/icon-diamond.png", alt="")
                    span(v-text="userInfo.diamonds")
                    i +
                span.gift-send(:class="{'selected' : selectedGift.prop_id}" @click="sendGift") 送礼
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'vue-awesome-swiper/node_modules/swiper/dist/css/swiper.min.css'
import { mapState } from 'vuex'
import queryString from 'queryString'
export default {
    props: ['isShowGift', 'gift_list', 'roomId'],
    components: {
        swiper,
        swiperSlide
    },
    data() {
        return {
            swiperOptionBanner: {
                initialSlide: 0,
                loop: false,
                pagination: '.swiper-pagination-gift',
                paginationClickable: false
            },
            selectedGift: {
                prop_id: null,
                diamonds: null
            }
        }
    },
    methods: {
        showGift() {
            this.$emit('showGift')
        },
        select(id, diamonds) {
            // 选择礼物
            if (this.selectedGift.prop_id == id) {
                this.selectedGift.prop_id = null;
                this.selectedGift.diamonds = null;
            }
            else {
                this.selectedGift.prop_id = id;
                this.selectedGift.diamonds = diamonds;
            }
        },
        sendGift() {
            // 发送礼物
            if (! this.roomId) {
                this.Toast("主播暂时不在家，无法发送礼物");
                return false;
            }
            if (! this.selectedGift.prop_id) {
                this.$emit('setPopupTip', '还没有选择礼物哦');
                return;
            }

            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/index' : this.api.popProp();
            this.axios.post(axiosUrl, queryString.stringify({
                "prop_id": this.selectedGift.prop_id,
                "room_id": this.roomId
            }))
                .then(res => {
                    if (res.data.status == 1) {
                        this.userInfo.diamonds = this.userInfo.diamonds - this.selectedGift.diamonds;
                    }
                    else {
                        this.$emit('setPopupTip', res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    this.$emit('setPopupTip', err || '网络异常，请求失败');
                });
        },
        showRecharge() {
            this.$emit('showRecharge');
            this.$emit('showGift');

        }
    },
    computed: {
        ...mapState(['userInfo'])
    },
    watch: {
        isShowGift() {
            if(this.isShowGift) {
                $(".gift-show-container").css({
                    "position":"fixed",
                    "top": "initial",
                    "bottom": $(this.$refs.giftMain).height()
                });
            }
            else{
                $(".gift-show-container").css({
                    "position":"absolute",
                    "bottom": "initial",
                    "top": 0
                });
            }
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.live-room-gift {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    opacity: 0;
    visibility: hidden;
    transition: all .3s;
    &.show {
        opacity: 1;
        visibility: visible;
    }
}

.gift-mask {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .1);
}

.gift-main {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    transition: -webkit-transform .15s ease;
    transform: translate3d(0, 100%, 0);
    &.show {
        transform: translate3d(0, 0, 0);
    }
    &.hide {
        transform: translate3d(0, 100%, 0);
    }
    .swiper-slide {
        height: auto;
    }
    .swiper-container{
        margin-bottom: -1px;
    }
}

.gift-list {
    background: linear-gradient(to bottom, rgba(20, 20, 20, .8) 0%, rgba(20, 20, 20, 1) 100%);
    display: flex;
    flex-wrap: wrap;
    padding: .4rem 0 1rem;
    .gift-item {
        position: relative;
        width: 25%;
        height: 50%;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: .375rem;
        border-right: 1px solid #444;
        border-bottom: 1px solid #444;
        &.selected {
            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                display: block;
                width: -webkit-fill-available;
                height: 100%;
                border: 1px solid @color-yellow;
            }
        }
        &:nth-child(5),
        &:nth-child(6),
        &:nth-child(7),
        &:nth-child(8) {
            border-bottom: 0;
        }
        h3 {
            font-size: .55rem;
            font-weight: 400;
        }
        .gift-icon {
            width: 80%;
            height: 2.5rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        img {
            max-width: 100%;
            max-height: 2.5rem;
        }
        .icon-selected {
            position: absolute;
            top: .3rem;
            right: .3rem;
            line-height: 1;
            font-size: .6rem;
        }
        .price {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: .55rem;
            img {
                height: .55rem;
                margin-right: .15rem;
            }
        }
    }
}

.gift-after {
    background-color: #0a0a0a;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .4rem;
}

.gift-send {
    height: 1.5rem;
    line-height: 1.5rem;
    padding: 0 .9rem;
    background-color: #aaa;
    border-radius: 1.5rem;
    font-size: .6rem;
    &.selected {
        background-color: @color-yellow;
    }
}

.gift-diamond {
    height: 1.5rem;
    border-radius: 1.5rem;
    display: flex;
    align-items: center;
    padding: 0 .5rem;
    background-color: #040404;
    color: #fff;
    img {
        height: .65rem;
    }
    img,
    span {
        margin-right: .5rem;
    }
    i {
        width: .8rem;
        height: .8rem;
        line-height: .8rem;
        text-align: center;
        border-radius: .65rem;
        background-color: #fee204;
        color: #333;
        font-family: 'arial';
        font-size: .8rem;
    }
}
</style>




