<template lang="pug">
    div.user-index-top
        div.mask
            img(:src="headImage", alt="")
        div.user-index-top-inner
            div.avator
                img.avator-img(:src="headImage", alt="")
                img.icon-vip(:src="vIcon", alt="" v-if="isAuthentication==2")
            div.nick-name
                span(v-text="nickName")
                img.icon-sex(:src="iconSex" alt="性别")
                img.icon-level(:src="iconLevel" alt="用户等级")
                span.edit(@click="showEdit")
                    i.icon.iconfont &#xe602;
            div.signature
                span(v-text="signature" v-if="signature")
                span(v-else) Ta好像忘记签名了
            div.ID(v-text="`账号：${ID}`")
            div.authentication(v-if="isAuthentication == 2")
                i.icon.iconfont &#xe603;
                span(v-text="`认证:${vExplain}`")
</template>
<script>
export default {
    props: ['headImage', 'nickName', 'iconSex', 'iconLevel', 'signature', 'ID', 'isAuthentication', 'vExplain', 'vIcon'],
    methods: {
        showEdit() {
            // 编辑用户资料
            this.$router.push({
                path: '/user/info/edit'
            })
        }
    }
}
</script>
<style lang="less" scoped>
.user-index-top {
    position: relative;
    width: 100%;
    height: 12.5rem;
    .mask {
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .85);
        img {
            width: 100%;
            height: 100%;
            filter: blur(10px);
            opacity: .2;
        }
    }
    .user-index-top-inner {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 999;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: .5rem;
        .nick-name {
            margin-top: .25rem;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            padding: 0 .5rem;
            .edit{
                margin-left: .125rem;
            }
        }
        .signature,
        .ID,
        .authentication {
            font-size: .6rem;
            margin-top: .25rem;
            display: flex;
            align-items: center;
        }
        .icon-sex {
            margin: 0 .125rem;
        }
        .authentication{
            .iconfont{
                margin-right: .15rem;
                font-size: .75rem;
            }
        }
    }
    .avator {
        position: relative;
        width: 4.5rem;
        height: 4.5rem;
        .avator-img {
            width: 4.5rem;
            height: 4.5rem;
            border-radius: 50%;
            border: 2px solid #fff;
        }
        .icon-vip {
            position: absolute;
            bottom: .25rem;
            right: .25rem;
            width: .6rem;
            height: .6rem;
        }
    }
}
</style>



