<template lang="pug">
    div.rank-income
        ul.swiper-pagination-subType.rankSumDayType(slot="pagination")
        mt-loadmore.loadmore-rank(
            :top-method="loadTop"
            :bottom-method="loadBottom"
            :bottom-all-loaded="paging[listType].allLoaded"
            :autoFill="autoFill"
            ref="loadmore"
            @top-status-change="handleTopChange"
            @bottom-status-change="handleBottomChange"
        )
            mint-loadmore-top(:topStatus="topStatus" slot="top")
            swiper.swiper-container-rank(:options="swiperOption" ref="swiperRankIncome" :style="{height: swiperContainerHeight+'px'}")
                swiper-slide(v-for="item in listCategory" key="")
                    div.data-container
                        template(v-if="item.type == 'day'")
                            rank-list(:list="list.day" :isLoaded="isLoaded" :allLoaded="paging[listType].allLoaded" :income ='income')
                        template(v-if="item.type == 'month'")
                            rank-list(:list="list.month" :isLoaded="isLoaded" :allLoaded="paging[listType].allLoaded" :income ='income')
                        template(v-if="item.type == 'all'")
                            rank-list(:list="list.all" :isLoaded="isLoaded" :allLoaded="paging[listType].allLoaded" :income ='income')
            mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="!paging[listType].allLoaded && isLoaded")
</template>
<script>
import Vue from 'vue'
import rankList from '@/components/rankList'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
import { mapGetters, mapActions } from 'vuex'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'vue-awesome-swiper/node_modules/swiper/dist/css/swiper.min.css'
import { Toast } from 'mint-ui'
export default {
    name: 'rankIncome',
    components: {
        swiper,
        swiperSlide,
        rankList,
        mintLoadmoreTop,
        mintLoadmoreBottom

    },
    data() {
        return {
            listWatch: 0,
            income: 1,
            list: {
                day: '',
                month: '',
                all: ''
            },
            paging: {
                day: {
                    page: 2,
                    has_next: '',
                    allLoaded: false
                },
                month: {
                    page: 2,
                    has_next: '',
                    allLoaded: false
                },
                all: {
                    page: 2,
                    has_next: '',
                    allLoaded: false
                }
            },
            topStatus: '',
            bottomStatus: '',
            listType: 'day',
            isLoaded: false,
            realIndex: 0,
            autoFill: false,
            allLoaded: false,
            listCategory: [
                {
                    title: '日榜',
                    type: 'day'
                },
                {
                    title: '月榜',
                    type: 'month'
                },
                {
                    title: '总榜',
                    type: 'all'
                },
            ],
            swiperContainerHeight: '',
            swiperOption: {
                initialSlide: 0,
                loop: false,
                pagination: '.swiper-pagination-subType',
                paginationClickable: true,
                onSlideChangeStart: swiper => {


                },
                onSlideChangeEnd: swiper => {
                    // console.log('onSlideChangeEnd', swiper.realIndex)
                },
                onInit: swiper => {

                },
                // 收入和消费
                paginationBulletRender: (swiper, index, className) => {
                    return `<li class="rankDay ${className}"><span class="item-title">${this.listCategory[index].title}</span></li>`;
                    // return `<li></li>`;
                }
            },
        }
    },
    beforeRouteEnter(to, from, next) {   // 路由进来之前
        next(vm => {
            vm.setFooterBarActiveName('rankIndex');
        });
    },
    created() {
        this.getData();
    },
    mounted() {
        // 操作DOM 
        
    },
    methods: {
        ...mapActions(['setFooterBarActiveName']),
        getData(isRefresh = false) {
            let self = this;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/rankConsumption' : self.api.rankConsumption();
            self.axios.all([self.axios.get(axiosUrl + '&rank_name=all'), self.axios.get(axiosUrl + '&rank_name=month'), self.axios.get(axiosUrl + '&rank_name=day')])
                .then(self.axios.spread(function(all, month, day) {
                    self.listWatch++;
                    self.isLoaded = true;

                    self.list.all = all.data.list;
                    self.list.month = month.data.list;
                    self.list.day = day.data.list;

                    self.paging.day = {
                        page: 2,
                        has_next: day.data.has_next,
                        allLoaded: day.data.has_next ? false : true
                    };
                    self.paging.month = {
                        page: 2,
                        has_next: month.data.has_next,
                        allLoaded: month.data.has_next ? false : true
                    };
                    self.paging.all = {
                        page: 2,
                        has_next: all.data.has_next,
                        allLoaded: all.data.has_next ? false : true
                    };

                    self.$nextTick(function() {
                        self.init()
                        isRefresh && self.$refs.loadmore.onTopLoaded();
                    });
                }))
                .catch(err => {
                    self.listWatch++;
                    Toast(err || '网络异常，请求失败');
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                });
        },
        loadTop() {
            let self = this;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/rankConsumption' : self.api.rankConsumption();
            self.axios.get(axiosUrl, {
                params: {
                    rank_name: this.listType
                }
            })
                .then(res => {
                    self.listWatch++;
                    if (res.data.status == 1) {
                        self.list[this.listType] = res.data.list;
                        self.paging[this.listType].page = 2;
                        self.paging[this.listType].has_next = res.data.has_next;
                        self.paging[this.listType].allLoaded = res.data.has_next ? false : true;
                    }
                    else {
                        Toast(res.data.error || '请求出错');
                    }

                    self.$nextTick(function() {
                        self.init()
                        self.$refs.loadmore.onTopLoaded();
                    });
                })
                .catch(err => {
                    self.listWatch++;
                    Toast(err || '网络异常，请求失败');
                    self.$refs.loadmore.onTopLoaded();
                });
        },
        loadBottom() {
            // 无限加载
            let self = this;
            console.log(screen.height)
            console.log($(".swiper-slide-active").find(".data-container").height())
            if (self.paging[self.listType].has_next) {
                let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/rankConsumption' : self.api.rankConsumption();
                self.axios.get(`${axiosUrl}&rank_name=${self.listType}`, {
                    params: {
                        page: self.paging[self.listType].page
                    }
                }).then(res => {

                    setTimeout(() => {
                        if (res.data.status == 1) {

                            self.paging[self.listType].has_next = res.data.has_next;
                            self.paging[this.listType].allLoaded = res.data.has_next ? false : true;
                            self.paging[self.listType].page = self.paging[self.listType].page + 1;

                            if (res.data.list && res.data.list.length && res.data.list.length > 0) {
                                for (let i = 0; i < res.data.list.length; i++) {
                                    self.list[self.listType].push(res.data.list[i]);
                                }
                            }
                            self.$nextTick(function() {
                                self.init();
                                self.$refs.loadmore.onBottomLoaded();
                            });
                        }
                        else {
                            Toast(res.data.error || '操作失败');
                        }
                        self.$refs.loadmore.onBottomLoaded();
                    }, 500);
                })
                    .catch(err => {
                        Toast(err || '网络异常');
                        self.$refs.loadmore.onBottomLoaded();
                    });
            }
            else {
                return false;
            }
        },
        init() {
            let self = this,
                liveContainer_h = $(".swiper-slide-active").find(".data-container").height()+10,
                screen_h = screen.height;
            self.swiperContainerHeight = liveContainer_h > (screen_h - 178) ? liveContainer_h : (screen_h - 178);

            self.swiperOption.initialSlide = 1;
            self.swiperOption.onSlideChangeStart = (swiper) => {
                window.scroll(0, 0);
                self.realIndex = swiper.realIndex;
                self.listType = self.listCategory[swiper.realIndex].type;


                let liveContainer_h = $(".swiper-slide-active").find(".data-container").height(),
                    screen_h = screen.height;
                self.swiperContainerHeight = liveContainer_h > (screen_h - 178) ? liveContainer_h : (screen_h - 178);
            };
        },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        },
        handleBottomChange(status) {
            // 获取上拉最新状态
            this.bottomStatus = status;
        }
    },
    watch: {
        // list() {
        //     // 当页面数据发生改变,进行处理
        //     this.paging[this.listType].allLoaded = !Boolean(this.paging[this.listType].has_next);
        // }
    }
}

</script>
<style lang="less" scoped>
@import '../../assets/css/variable.less';
.ranking-body {
    .data-container {
        margin-top: .5rem;
    }
}
</style>




