<template lang="pug">
    div.live-room-recharge(
        :class="{'show' : show}"
        @click.self="showRecharge"
    )
        div.live-room-recharge-inner
            i.icon.iconfont.close(@click="showRecharge") &#xe6a0;
            //- div.tab-header
            //-     div.tab-item.active
            //-         span 充值
            //-     div.tab-item
            //-         span 兑换
            div.tab-content
                div.recharge
                    div.btn-group
                        span.btn-choose.active wx
                        //- span.btn-choose 支付宝
                    ul.rule-list
                        li.rule-item(
                            v-for="item in rule_list"
                            @click="pay(item.id)"
                        )
                            div.diamond
                                span(v-text="item.diamonds")
                                img.icon-diamond(src="../../../assets/img/icon-diamond.png")
                            div.price(v-text="`售价:${item.money}`")
                    div.balance
                        span(v-text="`余额：${diamonds}`")
                        img.icon-diamond(src="../../../assets/img/icon-diamond.png")
                div.exchange


</template>
<script>
import queryString from 'queryString'
export default {
    props: ['show'],
    data () {
        return {
            diamonds: '',
            rule_list: []
        }
    },
    methods: {
        getData() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/payRecharge' : this.api.payRecharge();
            this.axios.get(axiosUrl)
                .then(res => {
                    this.diamonds = res.data.diamonds;
                    this.rule_list = res.data.rule_list;
                })
                .catch(err => {
                    console.log(err);
                });
        },
        showRecharge() {
            this.$emit('showRecharge')
        },

        /**
         * @method:请求支付
         */
        pay(rule_id) {
            this.axios.post(this.api.payWeixin(), queryString.stringify({
                rule_id: rule_id
            }))
            .then(res => {
                if(res.data.status == 1) {
                    this.weixinPay(result.data); // 微信支付
                }
                else {
                    this.Toast(res.data.error);
                }
            })
            .catch(err => {
                this.Toast(err);
            });
        },

        /**
         * @method :微信支付方法
         * @param data
         */
        weixinPay: function(data) {
            var self = this;
            if (typeof WeixinJSBridge == "undefined") {//微信浏览器内置对象。参考微信官方文档
                if (document.addEventListener) {
                    document.addEventListener('WeixinJSBridgeReady', self.onBridgeReady(data), false);
                } else if (document.attachEvent) {
                    document.attachEvent('WeixinJSBridgeReady', self.onBridgeReady(data));
                    document.attachEvent('onWeixinJSBridgeReady', self.onBridgeReady(data));
                }
            } else {
                self.onBridgeReady(data);
            }
        },

        /**
         * @method 支付费用方法
         * @param data:后台返回的支付对象,(详情微信公众号支付API中H5提交支付);
         */
        onBridgeReady: function(data) {
            var self = this;
            WeixinJSBridge.invoke(
                'getBrandWCPayRequest', {
                    "appId": data.appId, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                    "timeStamp": data.timeStamp, //时间戳
                    "nonceStr": data.nonceStr, // 支付签名随机串，不长于 32 位
                    "package": data.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
                    "signType": data.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                    "paySign": data.paySign // 支付签名
                },
                function(res) {
                    // 支付成功
                    if (res.err_msg == "get_brand_wcpay_request：ok") {
                        self.$router.push("/reservedBerth");
                    }
                    // 支付失败
                    else {
                        alert("支付失败,请跳转页面" + res.err_msg);
                    }
                }
            );
        }
    },
    watch: {
        show() {
            this.getData();
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.icon-diamond{
    height: .7rem;
    margin-left: .15rem;
}
.live-room-recharge {
    position: absolute;
    z-index: 99;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translate3D(0, 100%, 0);
    &.show {
        transition: all .3s;
        transform: translate3D(0, 0, 0);
    }
    &.mask {
        background-color: rgba(0, 0, 0, .5);
    }
}
.live-room-recharge-inner{
    position: relative;
    width: 15rem;
    background-color: #fff;
    border-radius: 5px;
    padding: 1.5rem 0 1rem;
    .close{
        position: absolute;
        right: .5rem;
        top: .5rem;
        line-height: 1;
        font-size: .75rem;
    }
    .tab-header{
        height: 1.5rem;
        display: flex;
        justify-content: space-around;
        align-content: center;
        margin-bottom: 1rem;
        .tab-item{
            width: 100%;
            position: relative;
            text-align: center;
            color: @fc-light;
            &:after{
                position: absolute;
                top: 0;
                right: 0;
                content: '';
                display: block;
                width: .05rem;
                height: .9rem;
                background-color: #e7e7f1;
            }
            &:last-child{
                &:after{
                    content: none;
                }
            }
            &.active{
                span {
                    padding:0 .5rem .3rem;
                    color: @color-theme;
                    border-bottom: 2px solid @color-theme;
                }
            }
        }
    }
    .tab-content{
        
    }
    .recharge{
        .btn-group{
            display: flex;
            justify-content: space-evenly;
        }
        .btn-choose{
            width: 4.5rem;
            height: 1.25rem;
            line-height: 1.25rem;
            text-align: center;
            border-radius: 1.25rem;
            display: block;
            background-color: #fff;
            border: 1px solid @color-theme;
            &.active{
                background-color: @color-theme;
                color: #fff;
            }
        }
        .rule-list{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-evenly;
            padding-top: 1rem;
            margin-top: 1rem;
            border-top:1px solid @bdcolor;
            max-height: 10rem;
            overflow: hidden;
            overflow-y: scroll;
            .rule-item{
                width: 6rem;
                height: 3rem;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                border:1px solid @color-theme;
                border-radius: 5px;
                margin-bottom: 1rem;
                .diamond{
                    display: flex;
                    align-items: center;
                }
                .price{
                    color: @fc-light;
                    font-size: .6rem;
                }
            }
        }
        .balance{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 1rem;
        }
    }
}
</style>




