<template lang="pug">
    div.live-room-playerEnded
        div.playerEnded-main
            div.playerEnded-top
                h3 直播已经结束
                img.avator(:src="headImage")
                div.nick-name(v-text="nickName")
                div.viewer-num
                    label 观看人数
                    span(v-text="viewerNum")
            div.playerEnded-bottom
                div.btn(@click="follow" v-text="isFollowText")
                router-link.btn(to="/") 返回首页
        img.bg-img(:src="headImage")
</template>
<script>
import { mapGetters } from 'vuex'
export default {
    props: ['headImage', 'nickName', 'viewerNum', 'podcastId', 'hasFocus'],
    data() {
        return {
            isFollow: this.hasFocus
        }  
    },
    methods: {
        follow() {
            // 关注                
            this.axios.get(this.api.userFollow(), {
                params: {
                    to_user_id: this.podcastId
                }
            })
                .then(res => {
                    if (res.data.status == 1) {
                        this.isFollow = res.data.has_focus;
                    }
                    else {
                        Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    Toast(err || '请求失败');
                });
        }
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        }),
        isFollowText() {
            return this.isFollow ? '已关注' : '关注';
        }
    }
}
</script>

<style lang="less" scoped>
    .live-room-playerEnded{
        position: absolute;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, .9);
        display: flex;
        justify-content: center;
        align-items: center;
        .playerEnded-main{
            position: absolute;
            width: 100%;
            height: 100%;
            color: #fff;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 3.5rem .75rem;
            z-index: 999;
        }
        .btn{
            height: 2rem;
            line-height: 2rem;
            width: 100%;
            border: 1px solid #fff;
            border-radius: 2rem;
            color: #fff;
            text-align: center;
            margin-top: .5rem;
            display: block;
        }
        .bg-img{
            position: absolute;
            height: 100%;
            filter: blur(30px);
            opacity: .5;
        }
        .playerEnded-top{
            width: 100%;
            text-align: center;
            font-size: .6rem;
            h3 {
                font-size: .8rem;
            }
            .avator{
                width: 4.5rem;
                height: 4.5rem;
                border-radius: 4.5rem;
                margin-top: 1.5rem;
            }
            .nick-name{
                margin-top: .25rem;
            }
            .viewer-num{
                margin-top: 1rem;
                label{
                    display: block;
                }
            }
        }
    }
</style>




