<template lang="pug">
    div.live-room-user-info(
        :class="[{'show' : show}, {'mask' : isShowTipoff}]"
        @click.self="showUserInfo"
    )
        div.live-room-user-info-inner(v-show="!isShowTipoff")
            div.inner-main
                div.inner-header
                    span(@click="tipoff()") 举报
                    i.icon.iconfont(@click="showUserInfo") &#xe66a;
                div.inner-content
                    template(v-if="isLoaded")
                        div.avator-box
                            div.avator
                                img.main-avator(:src="user.head_image || ''")
                                img.sub-avator(:src="cuser_list[0].head_image || ''" v-if="cuser_list[0]")
                        div.nick-name
                            span(v-text="user.nick_name || ''")
                            img.icon-sex(:src="iconSex" alt="性别")
                            img.icon-level(:src="iconLevel" alt="用户等级")
                        div.user-id
                            span.sp-l(v-text="`${appInit.account_name}:${user.id || ''}`")
                            span.sp-r
                                i.icon.iconfont &#xe609;
                                span(v-text="`${user.province} ${user.city}`")
                        div.signature
                            span(v-html="`${user.signature || 'TA好像忘记签名了'}`") 
                        div.total
                            span(v-text="`关注:${user.focus_count || ''}`")
                            span(v-text="`粉丝:${user.fans_count || ''}`")
                            span 
                                i(v-text="`送出:${user.use_diamonds}`")
                                img(src="../../../assets/img/icon-diamond.png" height="14" style="margin-left:.25rem;")
                            span(v-text="`秀票:${user.ticket}`")
                    div.mt-spinner-box(v-else)
                        mt-spinner(
                            :type="0"
                            color="#333"
                        )
            div.inner-footer
                span(
                    @click="follow"
                    v-text="`${has_focus ? '已关注' : '关注'}`"
                    :class="{ 'focused': has_focus }"
                )
                //- span 私信
                //- span 回复
                router-link(:to="{ path : '/user/others' , query : { to_user_id : toUserId }}") 主页
        div.tipoff-inner(v-show="isShowTipoff")
            div.inner-title
                span 举报类型
            div.inner-main
                mt-loadmore.mt-loadmore-tipoff(
                    :top-method="loadTop"
                    :autoFill="autoFill"
                    ref="loadmore"
                    @top-status-change="handleTopChange"
                )
                    mint-loadmore-top(:topStatus="topStatus" slot="top")
                    mt-radio(
                        class="mt-radio-tipoff"
                        v-model="value"
                        :options="radio_tipoff_type_list"
                        align="right"
                    )
            div.inner-footer
                span.sp-l(@click="cancelTipoff") 取消
                span(@click="saveTipoff") 确定
</template>
<script>
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import { mapGetters } from 'vuex'
export default {
    props: ['show', 'toUserId', 'roomId'],
    components: {
        mintLoadmoreTop
    },
    data() {
        return {
            user: '',
            cuser_list: '',
            has_focus: '',
            iconLevel: '',
            iconSex: '',
            value: '',
            radio_tipoff_type_list: [],
            tipoff_type_list: '',
            tipoff_type: '',
            isShowTipoff: false,
            topStatus: '',
            autoFill: false,
            isLoaded: false
        }
    },
    created() {

    },
    methods: {
        getData() {
            this.isLoaded = false;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_home' : this.api.getUserInfo();
            this.axios.get(axiosUrl, {
                params: {
                    to_user_id: this.toUserId
                }
            })
                .then(res => {
                    this.isLoaded = true;
                    if (res.data.status == 1) {
                        this.user = res.data.user;
                        this.cuser_list = res.data.cuser_list;
                        this.has_focus = res.data.has_focus;
                        this.iconLevel = require(`@/assets/img/rank/rank_${this.user.user_level}.png`);
                        this.iconSex = res.data.user.sex == 1 ? require(`@/assets/img/sex-man.png`) : require(`@/assets/img/sex-woman.png`);
                    }
                    else {
                        this.Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    this.isLoaded = true;
                    this.Toast(err || '网络异常，请求失败');
                })
        },
        showUserInfo() {
            this.$emit('showUserInfo')
        },
        follow() {
            // 关注该用户
            if (this.has_focus)
                return;
            this.axios.get(this.api.userFollow(), {
                params: {
                    to_user_id: this.toUserId
                }
            })
                .then(res => {
                    if (res.data.status == 1) {
                        this.has_focus = res.data.has_focus;
                    }
                    else {
                        this.Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    this.Toast(err || '请求失败');
                });
        },
        tipoff(isRefresh = false) {
            console.log(isRefresh);
            // 举报该用户
            !isRefresh && (this.isShowTipoff = !this.isShowTipoff);
            if (!isRefresh && !this.tipoff_type_list || true) {
                let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/tipoffType' : this.api.tipoffType();
                this.axios.get(axiosUrl)
                    .then(res => {
                        if (res.data.status == 1) {
                            this.tipoff_type_list = res.data.list;
                            for (let key in res.data.list) {
                                this.radio_tipoff_type_list.push(res.data.list[key].name);
                            }
                        }
                        else {
                            this.Toast(res.data.error || '操作失败');
                        }
                        this.$nextTick(function() {
                            if (isRefresh) {
                                this.$refs.loadmore.onTopLoaded();
                            }

                        });
                    })
                    .catch(err => {
                        this.Toast(err || '网络异常，请求失败');
                        this.$nextTick(function() {
                            if (isRefresh) {
                                this.$refs.loadmore.onTopLoaded();
                            }

                        });
                    });
            }

        },
        loadTop() {
            // 下拉刷新加载
            this.tipoff(true);
        },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        },
        saveTipoff() {
            // 提交举报
            let tipoff_type;
            for (let key in this.tipoff_type_list) {
                if (this.tipoff_type_list[key].name == this.value) {
                    tipoff_type = this.tipoff_type_list[key].id;
                }
            }
            if(!tipoff_type) {
                this.Toast('请选择举报类别');
                return false;
            }
            this.axios.get(this.api.userTipoff(), {
                params: {
                    to_user_id: this.toUserId,
                    type: tipoff_type,
                    room_id: this.roomId
                }
            })
                .then(res => {
                    if (res.data.status == 1) {
                        this.isShowTipoff = this.show = false;
                        this.Toast(res.data.error || '已收到举报信息，我们将尽快落实处理');
                    }
                    else {
                        this.Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    this.Toast(err || '网络异常，请求失败');
                });
        },
        cancelTipoff() {
            // 取消举报
            this.isShowTipoff = false;
            this.$emit('showUserInfo');
        }
    },
    watch: {
        toUserId() {
            (this.toUserId > 0) && this.getData();
        },
        show() {
            this.show && (this.isShowTipoff = false);
        }
    },
    computed: {
        ...mapGetters({
            appInit: 'getAppInit'
        })
    },
    beforeDestroy() {
        this.isShowTipoff = false;
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.live-room-user-info {
    position: absolute;
    z-index: 99;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translate3D(0, 100%, 0);
    &.show {
        transition: all .3s;
        transform: translate3D(0, 0, 0);
    }
    &.mask {
        background-color: rgba(0, 0, 0, .5);
    }
}

.live-room-user-info-inner {
    width: 15rem;
    background-color: #fff;
    border-radius: 5px;
    .inner-main {
        padding: .5rem .75rem;
    }
    .inner-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        line-height: 1;
        margin-bottom: .5rem;
        .iconfont {
            line-height: 1;
            font-size: 1rem;
        }
    }
    .inner-content {
        // display: flex;
        // flex-direction: column;
        // justify-content: center;
        div {
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
    .avator-box {
        width: 100%;
        margin-bottom: 1rem;
    }
    .avator {
        position: relative;
        width: 3.5rem;
        height: 3.5rem;
        .main-avator {
            width: 3.5rem;
            height: 3.5rem;
            border-radius: 3.5rem;
        }
        .sub-avator {
            position: absolute;
            left: -2.5rem;
            top: .8875rem;
            width: 1.75rem;
            height: 1.75rem;
            border-radius: 1.75rem;
        }
    }
    .nick-name {
        .icon-sex {
            margin: 0 .25rem;
        }
    }
    .user-id,
    .signature {
        font-size: .6rem;
        padding-top: .2rem;
    }
    .user-id {
        .iconfont {
            font-size: .6rem;
            color: #b8b8b8;
        }
        .sp-l {
            margin-right: .25rem;
        }
    }
    .total {
        display: flex;
        flex-wrap: wrap;
        margin: 1.5rem -.75rem 1rem;
        span {
            position: relative;
            width: 50%;
            padding-left: 1.5rem;
            margin: .375rem 0;
            line-height: 1;
            &:after {
                position: absolute;
                top: 0;
                right: 0;
                content: '';
                display: block;
                width: 1px;
                height: .75rem;
                background-color: #eaeded;
            }
        }
    }
    .inner-footer {
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 2.5rem;
        border-top: 1px solid @bdcolor;
        span.focused {
            color: @fc-light;
        }
        a {
            color: @fc-main;
        }
    }
}

.tipoff-inner {
    width: 12rem;
    background-color: #fff;
    border-radius: 5px;
    .inner-title {
        padding: 1rem .5rem 0;
        text-align: center;
    }
    .inner-main {
        padding: .5rem;
        max-height: 9rem;
        overflow: hidden;
        overflow-y: scroll;
    }
    .inner-footer {
        height: 2.5rem;
        display: flex;
        border-top: 1px solid @bdcolor;
        span {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            &.sp-l {
                border-right: 1px solid @bdcolor;
            }
        }
    }
}
</style>



