import { im_message, convertMsg } from '@/assets/js/im-message.js'

function addMsg(msg, loginInfo, vueObj) {
    var time = webim.Tool.formatTimeStamp(msg.time);
    var data = convertMsg(msg, loginInfo, vueObj);
    if (!data) {
        return;
    }

    if (typeof data !== 'object') {
        data = {
            "user_level": 122,
            "nick_name": "[群提示消息]",
            "text": data,
        };
    }

    if (data.type == 1) {
        var sender = data.ext.sender;
        var gif = data.ext.pc_gif || data.ext.pc_icon || data.ext.icon;
        showGift([{
            nick_name: sender.nick_name,
            fromId: sender.user_id,
            num: data.ext.plus_num,
            giftId: data.ext.prop_id,
            gift_type: data.ext.desc,
            gift_image: gif,
            head_image: sender.head_image,
        }]);
    }

    // 收到的弹幕消息
    // if (data.type == 2 && showBarrage && player) {
    //     var barrage = [
    //         { "type": "content", "content": data.text, "time": "0" },
    //     ];
    //     player.addBarrage(barrage);
    // }

    if(vueObj.msg.length >= 50) {
        vueObj.msg.shift();
    }
    vueObj.msg.push(data); // 公屏消息列表
    
    // 收到礼物消息
    if(data.type == 1) {
        vueObj.podcast.ticket = data.ext.total_ticket; // 当前直播收到的总秀票值
    }

    // // 观众进入房间
    // if(data.type == 9 || data.type == 5) {
    //     for(let key in vueObj.viewer.list) {
    //         if(vueObj.viewer.list[key].user_id == data.ext.sender.user_id)
    //             return false;
    //     }
    //     vueObj.viewer.watch_number += 1;
    //     vueObj.viewer.list.unshift(data.ext.sender);
    // }

    // // 观众离开房间
    // if(data.type == 6) {
    //     vueObj.viewer.watch_number -= 1;
    //     for(let key in vueObj.viewer.list) {
    //         if(vueObj.viewer.list[key].user_id == data.ext.sender.user_id) {
    //             vueObj.viewer.list.splice(key, 1);
    //         }
    //     }
    // }

    // // 替换观众列表  50
    // if(data.type == 42) {
    //     vueObj.viewer.list = data.ext.sender;
    // }

    // 踢人
    if(data.type == 17) {
        vueObj.$router.go(-1);
    }
}

