<template lang="pug">
    div.content
        ul.rankMainType
            li.rankMainTypeItem
                span.item-title(:class="{'active' : activeName == 'income'}" @click="tabChange('income')") 收入榜
            li.rankMainTypeItem
                span.item-title(:class="{'active' : activeName == 'consum'}" @click="tabChange('consum')") 消费榜
        div.ranking-body
            transition(name='fade')
                rank-income(v-if="activeName == 'income'")
                rank-consum(v-else)
</template>
<script>
import Vue from 'vue'
import rankIncome from '@/views/rank/income'
import rankConsum from '@/views/rank/consum'
import { mapGetters, mapActions } from 'vuex'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'vue-awesome-swiper/node_modules/swiper/dist/css/swiper.min.css'
export default {
    name: 'rankIndex',
    components: {
        rankIncome,
        rankConsum
    },
    beforeRouteEnter(to, from, next) {   // 路由进来之前
        next(vm => {
            vm.setFooterBarActiveName('rankIndex');
            $(".page-content").scrollTop(0);
        });
    },
    data() {
        return {
            activeName: 'income'
        }
    },
    methods: {
        ...mapActions(['setFooterBarActiveName']),
        tabChange(name) {
            this.activeName = name;
        }
    },
    mounted() {
       
    }

}

</script>
<style lang="less" scoped>
@import '../../assets/css/variable.less';
.ranking-body {
    padding-top: 4.15rem;
    .data-container {
        margin-top: .5rem;
    }
}

.rankMainType {
    display: flex;
    justify-content: space-around;
    height: 2.15rem;
    line-height: 2.15rem;
    border-bottom: .05rem solid #e6e8f1;
    position: fixed;
    background: #fff;
    width: 100%;
    top: 0;
    z-index: 999;
    .rankMainTypeItem {
        font-size: .8rem;
        span {
            padding: 0 .5rem .5rem .5rem;
        }
        .active {
            border-bottom: 2px solid #333;
            color: #333;
            padding: 0 .5rem .5rem .5rem;
        }
    }
}
</style>




