const getOnlineGuest = state =>{
  return state.onlineGuest
};

const getFooterBarActiveName = state =>{
  return state.footerBarActiveName
};

const getIsShowHeadBar = state =>{
  return state.isShowHeadBar
};

const getIsShowFooterBar = state =>{
  return state.isShowFooterBar
};

const getAppInit = state =>{
  return state.appInit
};

const getUserInfo = state =>{
  return state.userInfo
};

const getLiveImage = state =>{
  return state.liveImage
};

export { getOnlineGuest, getFooterBarActiveName, getIsShowFooterBar, getAppInit, getUserInfo, getLiveImage }



