<template lang="pug">
    div.user-index-total
        div.total-item
            router-link(to="/user/tool/live_broadcast")
                label 直播
                span(v-text="videoCount")
        div.total-item
            router-link(to="/user/tool/small_video")
                label 小视频
                span(v-text="sVideoCount")
        div.total-item
            router-link(to="/user/tool/follow")
                label 关注
                span(v-text="focusCount")
        div.total-item
            router-link(to="/user/tool/fans")
                label 粉丝
                span(v-text="fansCount")
</template>
<script>
export default {
    props: ['videoCount', 'sVideoCount', 'focusCount', 'fansCount']
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.user-index-total {
    position: relative;
    padding: .75rem 0;
    display: flex;
    background-color: #fff;
    margin: .5rem;
    border-radius: 3px;
    margin-top: -1rem;
    .total-item {
        width: 100%;
        text-align: center;
        font-size: .6rem;
        border-right: 1px solid @bdcolor;
        a {
            color: @fc-light;
            label {
                display: block;
                font-size: .6rem;
            }
            span {
                color: @fc-main;
            }
        }
        &:last-child {
            border-right: 0;
        }
    }
}
</style>



