<template lang="pug">
    transition(name="fadeIn")
        div.live-room
            transition(name="fade")
                tip-wx-Share(
                    v-show="isShowTipWxShare",
                    v-on:showTipWxShare="showTipWxShare"
                )
            template(v-if="playerEnded")
                //- 直播已结束
                div.block-live-room-playerEnded
                    player-ended(
                        :viewerNum="viewer.watch_number"
                        :headImage="podcast.head_image"
                        :nickName="podcast.nick_name"
                        :podcastId="podcast.id"
                        :hasFocus="podcast.has_focus"
                    )
            template(v-else)
                //- 正在直播
                div.block-live-room-player(:class="{'pc-player' : video.create_type == 1}")
                    //- 直播封面
                    div.live-cover(
                        :style="{backgroundImage: 'url(' +liveImage + ')'}"
                        v-show="!videoPlaying && isFirstPlay && liveImage"
                    )
                        img.btn-start-play(
                            src="../../assets/img/btn-play.jpg"
                            ref="btnPlay"
                        )
                    //- 直播播放器
                    video-player.video-player-box(
                        ref="videoPlayer"
                        :options="videoOptions"
                        @play="onPlayerPlay($event)"
                        @pause="onPlayerPause($event)"
                        @ended="onPlayerEnded($event)"
                        @waiting="onPlayerWaiting($event)"
                        @playing="onPlayerPlaying($event)"
                        @loadeddata="onPlayerLoadeddata($event)"
                        @timeupdate="onPlayerTimeupdate($event)"
                        @canplay="onPlayerCanplay($event)"
                        @canplaythrough="onPlayerCanplaythrough($event)"
                        @statechanged="playerStateChanged($event)"
                        @ready="playerReadied"
                    )
                
                div.block-live-room-top
                    //- 贡献榜
                    contribution(
                        :viewer="viewer"
                        :podcast="podcast"
                        :roomId="video.id"
                        :liveIn="video.live_in"
                        :isLoaded="isLoaded"
                        v-on:showUserInfo = 'showUserInfo'
                    )
                    //- 收入
                    income(
                        :ticket="podcast.ticket"
                        :ID="podcast.id"
                        :isLoaded="isLoaded"
                    )
                
                //- 礼物动画
                div.block-live-room-giftshow(
                    :class="{'playback' : video.live_in != 1}"
                    v-show="isShowGiftShow"
                )
                    gift-show

                div.block-live-room-message(:class="{'playback' : video.live_in != 1}")
                    //- 消息公屏
                    message(
                        :msg="msg"
                        v-on:showUserInfo="showUserInfo"
                    )
                
                div.block-live-room-interact(v-show="isShowInteract && !isShowMsgForm")
                    //- 观众互动
                    interact(
                        v-on:showGift="showGift"
                        v-on:showMsgForm="showMsgForm",
                        v-on:showTipWxShare="showTipWxShare"
                    )
                
                //- 选择礼物列表
                gift(
                    :isShowGift="isShowGift"
                    :gift_list="gift_list"
                    :roomId="video.id"
                    v-on:showGift="showGift"
                    v-on:setPopupTip="setPopupTip"
                    v-on:showRecharge="showRecharge"
                )

                //- 发送文本层
                msg-form(
                    v-on:showMsgForm="showMsgForm"
                    v-on:sendMsg="sendMsg"
                    :isShowMsgForm="isShowMsgForm"
                    :loginInfo="loginInfo"
                    :avChatRoomId="avChatRoomId"
                )

                //- 用户信息
                user-info(
                    :show="isShowUserInfo"
                    :toUserId="showUserId"
                    :roomId="video.id"
                    v-on:showUserInfo="showUserInfo"
                )

                //- 充值
                recharge(
                    :show="isShowRecharge"
                    v-on:showRecharge="showRecharge"
                )

            //- 提示
            mt-popup.mint-popup-custom(
                v-model="popupVisible"
                position="top"
            )
                span(v-text="popupTip")
</template>
<script>
require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')
import { videoPlayer } from 'vue-video-player'
import contribution from '@/views/room/inc/contribution'
import income from '@/views/room/inc/income'
import interact from '@/views/room/inc/interact'
import message from '@/views/room/inc/message'
import gift from '@/views/room/inc/gift'
import playerEnded from '@/views/room/inc/playerEnded'
import msgForm from '@/views/room/inc/msgForm'
import userInfo from '@/views/room/inc/userInfo'
import recharge from '@/views/room/inc/recharge'
import giftShow from '@/views/room/inc/giftShow'
import tipWxShare from '@/components/tipWxShare'
import { im_message } from '@/assets/js/im-message.js'
import { chat } from '@/assets/js/chat.js'
import { mapGetters, mapActions } from 'vuex'
import wx from 'weixin-js-sdk'

export default {
    name: 'roomIndex',
    components: {
        videoPlayer, // 播放器
        contribution, // 秀票贡献排行 
        income, // 秀票收入
        interact, // 直播互动（底部）
        message, // 消息公屏
        gift, // 礼物列表
        playerEnded, // 直播结束
        msgForm, // 发送文本消息
        tipWxShare, // 微信分享提示
        userInfo, // 查看他人信息
        recharge, // 充值
        giftShow // 礼物动画
    },
    data() {
        return {
            podcast: {},
            viewer: {},
            video: {},
            prop_list: [],
            loginInfo: {},
            avChatRoomId: '',
            videoOptions: {},
            msg: [],
            popupTip: '',
            showUserId: '',
            playerEnded: false,
            videoPlaying: false,
            initChatRoomed: false,
            popupVisible: false,
            isLoaded: false,
            isShowGift: false, // 是否显示礼物列表，默认隐藏
            isShowMsgForm: false, // 是否显示发送文本层
            isShowInteract: false, // 是否显示观众互动
            isShowTipWxShare: false, // 是否显示分享提示
            isShowUserInfo: false, // 是否显示用户信息
            isShowRecharge: false, // 是否显示充值
            isShowGiftShow: false, // 是否显示礼物动画
            isFirstPlay: true, // 是否首次播放
            error: '' // 报错信息
        }
    },
    beforeRouteEnter: (to, from, next) => {
        next(vm => {

        });
    },
    created() {
        this.getData();
        for(let val of this.appInit.listmsg) {
            if(val.type == 9) {
                this.msg.push({
                    'nick_name': '[直播消息]',
                    'text': val.desc,
                    'ext': '',
                    'type': 9
                });
            }
        }
    },
    mounted() {
        let self = this;
        $(window).resize(function() {
            self.videoOptions.width = $(window).width();
            self.videoOptions.height = $(window).height();
        });

        this.wxShare({
            title: '分享标题哦',
            desc: '分享详情哦',
            link: location.href,
            imgUrl: require('@/assets/img/avator-big.jpg')
        });
    },
    methods: {

        /**
         * @method :初始化数据
         */
        getData() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? 'gm/api/getVideo' : `${this.api.getVideo()}&id=${this.$route.params.id}`;
            this.axios.get(axiosUrl)
                .then(res => {
                    this.isLoaded = true;
                    this.podcast = res.data.podcast;
                    this.viewer = res.data.viewer;
                    this.video = res.data.video;
                    this.prop_list = res.data.prop_list;
                    this.error = res.data.error;

                    this.$nextTick(() => {
                        // 动态修改微信浏览器title
                        document.title = res.data.video.live_in == 1 ? `${res.data.podcast.nick_name}正在直播` : `${res.data.podcast.nick_name}的回播`;
                    });

                    if (res.data.video.group_id) {
                        let tim = res.data.tim;
                        this.loginInfo = {
                            'sdkAppID': tim.sdkappid,
                            'appIDAt3rd': tim.sdkappid,
                            'accountType': tim.account_type,
                            'identifier': tim.account_id,
                            'userSig': tim.usersig,
                            'identifierNick': this.userInfo.nick_name,
                            'head_image': this.userInfo.head_image,
                            'user_level': this.userInfo.user_level
                        };
                        this.avChatRoomId = res.data.video.group_id;
                    }
                })
                .catch(err => {
                    console.log(err);

                });
        },

        initChatRoom() {
            // 初始化实时聊天
            let _self = this, chatObj = new chat(this.loginInfo, this.avChatRoomId, this);

            im_message.init(this.loginInfo, chatObj.listeners);
            _self.$nextTick(function() {
                setTimeout(function() {
                    _self.initChatRoomed = _self.isShowInteract = _self.isShowGiftShow = true;
                }, 50);
            });

        },
        sendMsg(msgText) {
            // 发送消息
            im_message.sendTextMsg(this.loginInfo, this.avChatRoomId, msgText);
        },
        setPopupTip(popupTip) {
            // 顶部消息提示
            let self = this;
            [self.popupTip, self.popupVisible] = [popupTip, true];
            setTimeout(function() {
                [self.popupTip, self.popupVisible] = ['', false];
            }, 2000);
        },
        
        /**
         * @method :显隐事件
         */
        showGift() {
            // 显隐 礼物列表
            this.isShowGift = !this.isShowGift;
        },
        showTipWxShare() {
            // 显隐 微信分享提示
            this.isShowTipWxShare = !this.isShowTipWxShare;
        },
        showMsgForm() {
            // 显隐 发言输入框
            this.isShowMsgForm = !this.isShowMsgForm;
        },
        showUserInfo(user_id) {
            // 显隐 他人信息
            this.isShowUserInfo = ! this.isShowUserInfo;
            this.showUserId = user_id;
        },
        showRecharge() {
            // 显隐 充值
            this.isShowRecharge = ! this.isShowRecharge;
        },

        /**
         * @method :播放器监听事件
         * @param player
         */
        onPlayerPlay(player) {
            // 触发播放
            this.isFirstPlay = false;
        },
        onPlayerPause(player) {
            // 播放已暂停
            this.videoPlaying = false;
        },
        onPlayerEnded() {
            // 直播完成
            this.videoPlaying = false;
        },
        onPlayerWaiting(player) {
            // 播放正在等待
        },
        onPlayerPlaying(player) {
            // 正在播放
            this.videoPlaying = true;
        },
        onPlayerLoadeddata(player) {
            // 播放器需要的数据已就绪
        },
        onPlayerTimeupdate(player) {
            // 播放时间更新
        },
        onPlayerCanplay(player) {
            // 可以播放
            console.log('可以播放');
        },
        onPlayerCanplaythrough(player) {

        },
        playerStateChanged(playerCurrentState) {
            // console.log('player current update state', playerCurrentState);
        },
        playerReadied(player) {
            // 播放器已初始化就绪
            // you can use it to do something...
            // player.[methods]

            $(this.$refs.btnPlay).on('click', function() {
                player.play();
            });

            $("video").attr({
                "x5-video-player-type": "h5",
                "x5-video-player-fullscreen": "true",
                "playsinline": !0
            });
        },    
        // play() {
        //     alert('play');
        //     videojs($('video').attr("id")).ready(function(){
        //         alert('play2');
        //         this.play();
        //     });
        // }
    },
    watch: {
        videoPlaying() {
            let self = this;
            // if (!self.initChatRoomed && self.videoPlaying) {
            //     self.initChatRoom();
            // }
        },
        video() {
            let _self = this,
                sources_type,
                win_w = $(window).width(),
                win_h = $(window).height();
            if(_self.error) {
                _self.setPopupTip(_self.error);
                setTimeout(function() {
                    _self.$router.go(-1);
                }, 2500);
                return false;
            }

            switch (_self.video.live_in) {
                case '1':
                    sources_type = 'application/x-mpegURL';
                    break;
                case '3':
                    sources_type = 'video/mp4';
                    break;
            }

            _self.videoOptions = {
                // videojs and plugin options
                width: win_w,
                height: win_h,
                sources: [{
                    withCredentials: false,
                    type: sources_type,
                    src: _self.video.play_url
                }],
                autoplay: _self.video.live_in == 1 ? true : false,
                controls: _self.video.live_in == 1 ? false : true,
                controlBar: {
                    captionsButton: false,
                    chaptersButton: false,
                    playbackRateMenuButton: false,
                    LiveDisplay: true,
                    subtitlesButton: false,
                    currentTimeDisplay: true,
                    remainingTimeDisplay: true,
                    progressControl: true,
                    volumeMenuButton: {
                        inline: false,
                        vertical: true
                    },
                    fullscreenToggle: false
                },
                language: 'zh-CN',
                fullscreen: true,
                flash: { hls: { withCredentials: false } },
                html5: { hls: { withCredentials: false } },
                poster: _self.video.live_image || _self.video.head_image
            };

            if (!_self.initChatRoomed) {
                _self.initChatRoom();
            }
        }
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo',
            appInit: 'getAppInit',
            liveImage: 'getLiveImage'
        }),
        gift_list() {
            let result = [];
            for (var i = 0, len = this.prop_list.length; i < len; i += 8) {
                result.push(this.prop_list.slice(i, i + 8));
            }
            return result;
        }
    }
}
</script>
<style lang="less">
.block-live-room-top {
    position: absolute;
    top: .5rem;
    width: 100%;
    z-index: 9;
}

.live-room {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.block-live-room-player {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 9;
    .cover {
        width: 100%;
        height: 100%;
    }
}

.block-live-room-interact {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 9;
}

.block-live-room-message {
    position: absolute;
    bottom: 2.575rem;
    left: 0;
    z-index: 9;
    width: 100%;
    &.playback {
        bottom: 4.075rem;
    }
}

.block-live-room-form {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 9;
}
.block-live-room-giftshow {
    position: absolute;
    bottom: 48vh;
    left: 0;
    width: 100%;
    z-index: 9;
    &.playback {
        bottom: 54vh;
    }
}
.live-cover{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    .btn-start-play{
        width: 2.5rem;
        height: 2.5rem;
    }
}
</style>



