<template lang="pug">
    div.app.views
        //- header-bar(v-if="isShowHeadBar")
        footer-bar(:activeName="footerBarActiveName" v-if="isShowFooterBar")
        div.page-content
            keep-alive(:include="['index', 'rankIndex', 'videoIndex', 'userIndex']")
                router-view
</template>

<script>
import footerBar from '@/components/footer'
import { mapState } from 'vuex'
export default {
    name: 'app',
    components: {
        footerBar
    },
    computed: {
        ...mapState(['footerBarActiveName', 'isShowFooterBar', 'isShowHeadBar'])
    }
}
</script>
<style lang="less">
@import './assets/css/reset.css';
@import './assets/css/variable.less';
@import './assets/css/public.less';
@import './assets/css/mintUi.less';
@import './assets/css/sui.css';
@import './assets/css/pages.less';
@import './assets/css/swiperPagination.less'; // 轮播分页器自定义样式
@import './assets/css/videoPlayer.less'; // 视频播放器自定义样式
@import './assets/css/vueCoreImageUpload.less'; // 裁切图片自定义样式
body {
    margin: 0;
    background-color: #f8f8f8;
}

.video-js .vjs-tech {
    width: initial;
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity .3s
}

.fade-enter,
.fade-leave-to {
    opacity: 0
}

.fadeIn-enter-active {
    transition: opacity .3s
}

.fadeIn-enter {
    opacity: 0
}

.mt-spinner-box{
    display: flex;
    justify-content: center;
    padding: 1rem;
}
</style>



