<template lang="pug">
    div.content
        div.place-video-list
            mt-loadmore(
                :top-method="loadTop"
                :bottom-method="loadBottom"
                :bottom-all-loaded="allLoaded"
                :autoFill="autoFill"
                ref="loadmore"
                @top-status-change="handleTopChange"
                @bottom-status-change="handleBottomChange"
            )
                mint-loadmore-top(:topStatus="topStatus" slot="top")
                template(v-if="isLoaded")
                    svideo-list(:list="dataList" :allLoaded="allLoaded")
                div.mt-spinner-box(v-else)
                    mt-spinner(
                        :type="0"
                        color="#333"
                    )
                mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="!allLoaded && isLoaded")  
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import { Indicator } from 'mint-ui'
import nullData from '@/components/nullData'
import svideoList from '@/components/svideoList'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
export default {
    name: 'videoIndex',
    components: {
        nullData,
        svideoList,
        mintLoadmoreTop,
        mintLoadmoreBottom
    },
    data() {
        return {
            dataList: '',
            has_next: '',
            page: 2,
            autoFill: false,
            allLoaded: false,
            isLoaded: false,
            topStatus: '',
            bottomStatus: ''
        }
    },
    beforeRouteEnter(to, from, next) {
        next(vm => {
            vm.setFooterBarActiveName('videoIndex');
            $(".page-content").scrollTop(0);
        });
    },
    created() {
        this.getData();
    },
    mounted() {
    },
    methods: {
        ...mapActions(['setFooterBarActiveName']),
        getData(isRefresh = false) {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/svideo' : this.api.getSvideo();
            this.axios.get(axiosUrl).then(res => {
                this.isLoaded = true;
                if (res.data.status == 1) {
                    this.dataList = res.data.list;
                    this.has_next = res.data.has_next;
                    this.page = 2;
                }
                else {
                    this.Toast(res.data.error || '请求出错');
                }

                this.$nextTick(function() {
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                });

            })
                .catch(err => {
                    this.Toast(err || '网络异常，请求失败');
                });
        },
        loadTop(id) {
            // 下拉刷新加载
            var self = this;
            self.getData(true);
        },
        loadBottom() {
            // 无限加载
            let self = this;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/svideo' : self.api.getSvideo();
            if (self.has_next) {
                self.axios.get(axiosUrl, {
                    params: {
                        page: self.page
                    }
                }).then(res => {
                    setTimeout(() => {
                        if (res.data.status == 1) {

                            self.has_next = res.data.has_next;
                            self.page = self.page + 1;

                            if (res.data.list && res.data.list.length && res.data.list.length > 0) {
                                for (let i = 0; i < res.data.list.length; i++) {
                                    self.dataList.push(res.data.list[i]);
                                }
                            }
                        }
                        else {
                            self.Toast(res.data.error || '请求出错');
                        }

                        self.$nextTick(function() {
                            self.$emit('init');
                            self.$refs.loadmore.onBottomLoaded();
                        });
                    }, 500);
                })
                    .catch(err => {
                        self.Toast(err || '网络异常，请求失败');
                        self.$refs.loadmore.onBottomLoaded();
                    });
            }
            else {
                return false;
            }
        },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        },
        handleBottomChange(status) {
            // 获取上拉最新状态
            this.bottomStatus = status;
        }
    },
    watch: {
        dataList() {
            // 当页面数据发生改变,进行处理
            this.allLoaded = !Boolean(this.has_next);
        }
    }
}
</script>


